import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DialogService } from 'app/shared/services/dialogs/dialog.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-supervisor-approval-stage',
  templateUrl: './supervisor-approval-stage.component.html',
  styleUrls: ['./supervisor-approval-stage.component.scss']
})
export class SupervisorApprovalStageComponent implements OnInit {
  isApprovalDetails: boolean= false;
  isCardSupervisorApprovalDone:boolean=false;

  constructor(private router: Router,private dialogService : DialogService,private ls: LocalStoreService,
   ) { }

  ngOnInit(): void {
    this.isApprovalDetails = this.ls.getItem('CARD_APPROVAL_DONE');
    this.isCardSupervisorApprovalDone=this.ls.getItem('CARD_SUPERVISOR_DONE');
    console.log("", this.isApprovalDetails)
  }

  addDetails(){
    console.log("calling")
    this.router.navigate(["/cardaccount/SupervisorApprovalStage/list/approvalDetails"]);
  }
  openApprovalSummary(){
    this.dialogService.CardApprovalSummary();
  }
  openViewApprovalSummary(){
    this.dialogService.CardApprovalViewSummary();
  }
}
