import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DialogService } from 'app/shared/services/dialogs/dialog.service';
import { Observable, of, Subscription } from 'rxjs';
// import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AccountService } from 'app/views/card-account/account.service';

@Component({
  selector: 'app-card-account',
  templateUrl: './card-account.component.html',
  styleUrls: ['./card-account.component.scss']
})
export class CardAccountComponent implements OnInit {
  public accountItems: any[];
  private accountItemsSub: Subscription;
  isCardApplicationStageDone : boolean = false;
  isCardInitiationDone : boolean = false;
  isCardCustomerInformationScreen: boolean = false;
  isCardFinancialDetailsScreen : boolean = false;
  //isCardPreferencesScreen : boolean = true;
  isCardPrefrenceDone:boolean = false;
  // isAccountServices : boolean = true;
  isCardAccountService:boolean=false;
  isAddOnCardHolderDetails : boolean = true;

  isDocumentUploadDone : boolean = false;
  isDocumentUpload : boolean = false;

  isCardApplicationEnrichmentDone:boolean=false
 // isEnrichmentDone : boolean = true;
 isCardInterestDetails : boolean = false;
 isCardChargeDetails : boolean = false;

  isApplicationUnderWritingStageDone : boolean = true;
 // isCardCreditRatingDetails : boolean = true;
  isQualitativeScoreboard : boolean = true;
  isCardCreditRatingDetails: boolean = false;
  isCardQualitativeScoreboard: boolean=false;

  isCardAssessmentStageDone : boolean = false;
  isAssessmentReport : boolean = false;


  isCardSupervisorApprovalDone : boolean = true;
  isApprovedDetails : boolean = true;

  isCardDigitalSigningDone : boolean = true;
  isCardDigitalSigning : boolean = true;
  cardId: any;







  
  constructor(
    private ls: LocalStoreService,private api: ApiService, private router: Router,private cdr:ChangeDetectorRef ,private accountService:AccountService
  ) { }

  ngOnInit(): void {
    this.accountItemsSub = this.accountService.accountItems$.subscribe(menuItem => {
      this.accountItems = menuItem;
    });
    console.log("reset")
    this.cardId = this.ls.getItem('cardId');
    this.isCardApplicationStageDone=this.ls.getItem('CARD_APPLICATION_ENTRY_DONE');
    this.isCardInitiationDone=this.ls.getItem('CARD_INITIATION_DONE');
    this.isCardCustomerInformationScreen=this.ls.getItem('Card_Customer_Done');
    this.isCardFinancialDetailsScreen = this.ls.getItem('CARD_FINANCIAL_DONE');
    // this.isCardPreferencesScreen = this.ls.getItem('CARD_PREFERENCE_DONE');
    this.isCardPrefrenceDone=this.ls.getItem('card_prefrence_done');
    // this.isAccountServices = this.ls.getItem('CARD_ACCOUNT_SERVICES_DONE');
    this.isCardAccountService=this.ls.getItem('CARD_ACC_SERVICE_DONE');

    this.isDocumentUploadDone = this.ls.getItem('CARD_DOCUMENT_STAGE_DONE');
    this.isDocumentUpload=this.ls.getItem('CARD_DOCUMENT_DONE');


    this.isCardApplicationEnrichmentDone=this.ls.getItem('CARD_ENRICHMENT_DONE');
    console.log('isCardApplicationEnrichmentDone', this.isCardApplicationEnrichmentDone);
    
   // this.isEnrichmentDone= this.ls.getItem('LOAN_ENRICHMENT_DONE');
    this.isCardInterestDetails=this.ls.getItem('CARD_INTEREST_DETAILS_DONE');
    this.isCardChargeDetails=this.ls.getItem('CARD_CHARGE_DETAILS_DONE');
    
    this.isApplicationUnderWritingStageDone= this.ls.getItem('CARD_UNDERWRITING_DONE');
    this.isCardCreditRatingDetails = this.ls.getItem('CARD_CREDIT_RATING_DONE');
    this.isCardQualitativeScoreboard=this.ls.getItem('CARD_QUALITATIVE_SCOREBOARD_DONE');

    this.isCardAssessmentStageDone=this.ls.getItem('CARD_ASSESSMENT_STAGE_DONE');
    this.isAssessmentReport=this.ls.getItem('CARD_ASSESSMENT_DONE');

    this.isCardSupervisorApprovalDone=this.ls.getItem('CARD_SUPERVISOR_DONE');
    this.isApprovedDetails=this.ls.getItem('CARD_APPROVAL_DONE');

    this.isCardDigitalSigningDone=this.ls.getItem('CARD_DIGITAL_STAGE_DONE');
    this.isCardDigitalSigning=this.ls.getItem('CARD_DIGITAL_SIGN_DONE');

    this.accountService.refresh.subscribe((res: any) => {
     
    this.cardId = this.ls.getItem('cardId');

    this.isCardApplicationStageDone=this.ls.getItem('CARD_APPLICATION_ENTRY_DONE');
    this.isCardInitiationDone=this.ls.getItem('CARD_INITIATION_DONE');
    this.isCardCustomerInformationScreen=this.ls.getItem('Card_Customer_Done');
    this.isCardFinancialDetailsScreen = this.ls.getItem('CARD_FINANCIAL_DONE');
    // this.isCardPreferencesScreen = this.ls.getItem('CARD_PREFERENCE_DONE');
    this.isCardPrefrenceDone=this.ls.getItem('card_prefrence_done');
    this.isCardAccountService=this.ls.getItem('CARD_ACC_SERVICE_DONE')
    // this.isAccountServices = this.ls.getItem('CARD_ACCOUNT_SERVICES_DONE');

    
    this.isDocumentUploadDone = this.ls.getItem('CARD_DOCUMENT_STAGE_DONE');
    this.isDocumentUpload=this.ls.getItem('CARD_DOCUMENT_DONE');
    
   // this.isEnrichmentDone= this.ls.getItem('LOAN_ENRICHMENT_DONE');
   this.isCardApplicationEnrichmentDone=this.ls.getItem('CARD_ENRICHMENT_DONE');
    this.isCardInterestDetails=this.ls.getItem('CARD_INTEREST_DETAILS_DONE');
    this.isCardChargeDetails=this.ls.getItem('CARD_CHARGE_DETAILS_DONE');
    
    this.isApplicationUnderWritingStageDone= this.ls.getItem('CARD_UNDERWRITING_DONE');
    this.isCardCreditRatingDetails = this.ls.getItem('CARD_CREDIT_RATING_DONE');
    this.isCardQualitativeScoreboard=this.ls.getItem('CARD_QUALITATIVE_SCOREBOARD_DONE');

    this.isCardAssessmentStageDone=this.ls.getItem('CARD_ASSESSMENT_STAGE_DONE');
    this.isAssessmentReport=this.ls.getItem('CARD_ASSESSMENT_DONE');

    this.isCardSupervisorApprovalDone=this.ls.getItem('CARD_SUPERVISOR_DONE');
    this.isApprovedDetails=this.ls.getItem('CARD_APPROVAL_DONE');

    this.isCardDigitalSigningDone=this.ls.getItem('CARD_DIGITAL_STAGE_DONE');
    this.isCardDigitalSigning=this.ls.getItem('CARD_DIGITAL_SIGN_DONE');
 
     })
  }
  ngOnDestroy() {
    if (this.accountItemsSub) {
      this.accountItemsSub.unsubscribe();
    }
  }

}
