import { Component, ElementRef, OnInit, QueryList, ViewChildren } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { home } from 'app/shared/models/cardPrefrences';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AccountService } from 'app/views/card-account/account.service';


@Component({
  selector: 'app-card-prefrences',
  templateUrl: './card-prefrences.component.html',
  styleUrls: ['./card-prefrences.component.scss']
})
export class CardPrefrencesComponent implements OnInit {
  //@ViewChildren("selectLang") langSelects: QueryList<ElementRef<HTMLSelectElement>>;
  
  home=new home();
  cardPrefrenceForm: FormGroup;
  arraylimitType: string[] = [];
  selectedOptions2: string[] = [];
  cardId:number;
  cardDetails: string[]=[];
  isdailylimit:boolean=false;
  isLimitTransaction:boolean=false;
  limiPrTrn: Number;
  limitDaily: number;
  isGreaterValue:boolean=false;
  selectedDocumentTypes2: string[] = [];
  cardPrefrenceOnLoad:any = [];
  isRowEnds:boolean=false;
  islimitType:boolean=false;
  isDLReadOnly: boolean = true;
  isLPTReadOnly:boolean=true;

  
  constructor(  private fb: FormBuilder, private router: Router, private apiService:ApiService, private snack: MatSnackBar, private ls: LocalStoreService,
    private accountService : AccountService) { }

  ngOnInit(): void {
    this.cardId=this.ls.getItem('cardId');
    this.arraylimitType=["Domestic Limit","International Limit","Daily Limit","ATM limit","POS Limit","Internet Limit"];
    //this.newArray=this.arraylimitType
    this.buildCardPrefrenceForm();
    let cardPrefrenceScreen: boolean = JSON.parse(this.ls.getItem('card_prefrence_done'));
    if (this.cardId!=null && cardPrefrenceScreen) {
      this.gettingCardPrefrenceDetail();
    }
    else{
      console.log("");
      //this.buildMandateForm();
    }
    
   
  }
  gettingCardPrefrenceDetail() {
    this.apiService.getCardPrefrence(this.cardId).subscribe((cardDetails) => {
      console.log(cardDetails);
      
        this.cardPrefrenceOnLoad = cardDetails;
        if(cardDetails){
          this.isdailylimit=true;
          this.isLimitTransaction=true;
          this.islimitType=true;
        }
        else{
          this.isdailylimit=false;
          this.isLimitTransaction=false;
        }
        for(let i=0; i<=this.cardPrefrenceOnLoad.length-1;i++){
          this.selectedOptions2.push(this.cardPrefrenceOnLoad[i].limitType);
        }
        console.log("All Transaction Details", cardDetails);
        this.buildCardPrefrenceForm(cardDetails, true);
      });
  }
  buildCardPrefrenceForm(data?, isResponse?:boolean) {
    this.cardPrefrenceForm = this.fb.group({
      cardPrefrences: this.fb.array([this.addMFormGroup()])
    });
    if(isResponse){
      this.addMandateToFormGroup(data);
      console.log(data);
      for(let i=0;i<=data.length-1;i++){
        data[i].dailyLimit=this.addComa(data[i].dailyLimit);
        data[i].limitPerTransaction=this.addComa(data[i].limitPerTransaction);
      }
      this.m.patchValue(data);
    }
  }
  addMandateToFormGroup(data?) {
    const numberOfItems = data.length;
    for (let index = 1; index < numberOfItems; index++) {
      this.m.push(this.fb.group({
        limitType: [''],
      dailyLimit: [''],
      limitPerTransaction: [''],
      }));
    }
    this.isdailylimit=true;
  }
  get f() { return this.cardPrefrenceForm.controls; }
  get m() { return this.f.cardPrefrences as FormArray; }
  newcard(){

  }
  addMFormGroup(): FormGroup {
    
    return this.fb.group({
      limitType: [''],
      dailyLimit: ['',[ Validators.maxLength(18),Validators.pattern("^[0-9]{10,12}$")]],
      limitPerTransaction: ['', [ Validators.maxLength(18),Validators.pattern("^[0-9]{10,12}$")]],
    });
    
  }
 
  addMandate(){
    this.isdailylimit=false;
    this.islimitType=false;
    this.isLimitTransaction=false;
    this.isLPTReadOnly=true;
    this.isDLReadOnly=true;
 console.log("this.selectedOptions2",this.selectedOptions2);
this.m.push(this.fb.group({
 limitType: [''],
 dailyLimit: [''],
 limitPerTransaction: [''],
}));
  }
  
  addComa(num1){
    console.log(num1)
  
    let finalNum = num1.toString().replace(/,/g, '').split(".");
    finalNum[0] = finalNum[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    let final = finalNum.join(".");
    return final;

  }
  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
        event.preventDefault();
    }
    this.isdailylimit=true;
    this.isLPTReadOnly=false
   }
   keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
        event.preventDefault();
    }
    this.isLimitTransaction=true;
   }
   OnInput(event: any, flag,k) {
      this.limiPrTrn = event.target.value;
      var dailyNum=this.removeComma(this.cardPrefrenceForm.value.cardPrefrences[k].dailyLimit);
      var LimitTxn=this.removeComma(this.limiPrTrn);
      if(!this.limiPrTrn){
        this.isLimitTransaction=false;
      }else{
        this.isLimitTransaction=true;
      }
    
    if( Number(LimitTxn) > Number(dailyNum)){
      this.isGreaterValue=true;
    }
    else if( Number(LimitTxn) < Number(dailyNum)){
      this.isGreaterValue=false;
    }
    else{
      this.isGreaterValue=false;
    }
    let payload={
      limitPerTransaction:this.addComa(this.limiPrTrn)
    }
    this.m.controls[k].patchValue(payload);


  //  this.cardPrefrenceForm.get('limitPerTransaction').setValue(this.addComa( this.limiPrTrn ));
  //console.log( this.addComa( this.limiPrTrn ));
  //this.m.controls[k].value.limitPerTransaction.setValue( this.addComa( this.limiPrTrn ))

  }
  
  OnInput1(event: any, flag,k) {
      this.limitDaily = event.target.value;
      if(!this.limitDaily){
        this.isdailylimit=false;
      }else{
        this.isdailylimit=true;
      }
      let payload=
      {
        dailyLimit:this.addComa(this.limitDaily)
      }
      this.m.controls[k].patchValue(payload);
  }
  onclick1(k){
    this.islimitType=true;
    this.isLimitTransaction=true;
    this.isdailylimit=true;
    this.isRowEnds=false;
    this.isLPTReadOnly=false;
    this.isDLReadOnly=false;
    const control = <FormArray>this.cardPrefrenceForm.controls['cardPrefrences'];
    control.removeAt(k);
    if(this.cardPrefrenceOnLoad==undefined){
      console.log("deleted")
    }
    else if(this.cardPrefrenceOnLoad[k]!=undefined){
    let id = this.cardPrefrenceOnLoad[k].preferenceId;
    this.apiService.deleteCardPrefrence(id)
    .subscribe(data => { 
      
    },
    
      err=>{
        if (err.status == 200) {
          this.snack.open('Record Deleted Successfully', 'OK', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });
        }


    }
    )}

      this.selectedOptions2.splice(k,1);
      console.log("console.log(this.selectedOptions2);",this.selectedOptions2);
      this.isdailylimit=true;
   
  }

  onSubmit(){
    var data :any;
   var payload:any[]=[];
   this.cardPrefrenceForm.value.cardPrefrences.cardId=this.cardId;
   if(this.cardPrefrenceOnLoad){
    for(let i =this.cardPrefrenceOnLoad.length;i<=this.cardPrefrenceForm.value.cardPrefrences.length-1;i++){
      data=this.cardPrefrenceForm.value.cardPrefrences[i];
      let newTransaction=Object.assign({},data,{cardId: this.cardId});
      newTransaction.dailyLimit=parseInt(this.removeComma(newTransaction.dailyLimit));
      newTransaction.limitPerTransaction=parseInt(this.removeComma(newTransaction.limitPerTransaction));
      payload.push(newTransaction);
      
    }
   }else{
    for(let i =0;i<this.cardPrefrenceForm.value.cardPrefrences.length;i++){
      data=this.cardPrefrenceForm.value.cardPrefrences[i];
      let newTransaction=Object.assign({},data,{cardId: this.cardId});
      newTransaction.dailyLimit=parseInt(this.removeComma(newTransaction.dailyLimit));
      newTransaction.limitPerTransaction=parseInt(this.removeComma(newTransaction.limitPerTransaction));
      payload.push(newTransaction);
      
    }
   }
    
    this.apiService.uploadCardPrefrence(payload).subscribe(data => {
      console.log("data", data);
      if (data != null) {
        this.snack.open('Card Preferences Details Saved!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        // this.ls.setItem('card_prefrence_done', true);
        // this.accountService.refresh.next();
        this.next();
      }
    });
    
  }
  removeComma(num:any){
    var numb =num.replace(/,/g, "");
    console.log("numb",numb)
    return numb;

  }
  goBack(){
    this.router.navigate([`/cardaccount/applicationEntryStage/list/financialDetails`]);
  }
  next(){
     this.ls.setItem('card_prefrence_done', true);
      this.accountService.refresh.next();
    this.router.navigate([`/cardaccount/applicationEntryStage/list/accountServices`]);
  }
  isDocumentOptionDisabled2 = (value) => {
    return this.selectedOptions2.includes(value);
  };

  changeValue2(event:any, i) {
    this.islimitType=true;
    if (this.selectedDocumentTypes2[i]) {
      this.selectedOptions2 = this.selectedOptions2.filter(
        (val) => val !== this.selectedDocumentTypes2[i]
      );
    }
    
    this.selectedDocumentTypes2[i] = event;
    this.selectedOptions2.push(event);
    if(this.selectedDocumentTypes2.length==this.arraylimitType.length){
      this.isRowEnds=true;
     
      console.log("isRowends",this.isRowEnds);
    }
    else{
      this.isRowEnds=false;
      console.log("isRowends",this.isRowEnds);
    }
    this.isDLReadOnly=false
  }
 

}
