import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AccountService } from 'app/views/card-account/account.service';

@Component({
  selector: 'app-approval-details',
  templateUrl: './approval-details.component.html',
  styleUrls: ['./approval-details.component.scss']
})
export class ApprovalDetailsComponent implements OnInit {
  approvalForm: FormGroup;
  arrayAccountType: string[];
  arrayAccountCurrency: string[];
  arrayUserRecomendation: string[];
  cardId: any;
  showEditOption: boolean = false;
  arrayAccountBranch: any;
  currency: any;

  constructor(private api: ApiService,
    private ls: LocalStoreService,private fb:FormBuilder,
    private apiService:ApiService,private router: Router,
    private accountService : AccountService) { }

  ngOnInit(): void {
    this.getUserRecomendation("user_recomendation");
    this.getAccountType("account_type")
    this.getAccountBranchDetails();
    
    this.buildformGroup();
    console.log(this.approvalForm);

    this.cardId=this.ls.getItem('cardId');
    console.log("this.cardId",this.cardId);
    this.getCardInitiationDetails();
    let isApprovalDetails: boolean = this.ls.getItem('CARD_APPROVAL_DONE');
    console.log("checc", isApprovalDetails)
    if(this.cardId!=null && isApprovalDetails ){
      this.showEditOption = true;
      this.getApprovalDetails();

    }
  }

  buildformGroup(data?){
    this.approvalForm = this.fb.group({
      accountType:[data ? data.accountType : '',Validators.required],
      productCode:[data ? data.productCode :'',Validators.required],
      userRecommendation:[data ? data.userRecommendation :'',Validators.required],
      accountBranch:[data ? data.accountBranch :'',Validators.required],
      accountCurrency:[data ? data.accountCurrency :'',Validators.required],
      productName:[data ? data.productName :'',Validators.required],
      existingValues:[data ? data.existingValues :'',Validators.required],
      remarks:[data ? data.remarks :'']
    })
  }
  onSubmit(){
    console.log("data", this.approvalForm.value);
    
    let payload={
      cardId: this.cardId,
      accountBranch: this.approvalForm.value.accountBranch,
      accountCurrency: this.approvalForm.value.accountCurrency,
      accountType: this.approvalForm.value.accountType,
      existingValues: this.approvalForm.value.existingValues,
      productCode: this.approvalForm.value.productCode,
      productName: this.approvalForm.value.productName,
      remarks: this.approvalForm.value.remarks,
      userRecommendation: this.approvalForm.value.userRecommendation,

    }
    this.apiService.upsertApprovalDetails(payload)
    .subscribe((data: any) => {

      console.log("data",data);
      
    });
    this.next();
  }
next(){
  this.ls.setItem('CARD_APPROVAL_DONE', true);
  this.ls.setItem('CARD_SUPERVISOR_DONE',true);
  this.accountService.refresh.next();
  this.router.navigate([`/cardaccount/DigitalSigning/list`]);

}
goBack(){
  this.router.navigate([`/cardaccount/SupervisorApprovalStage/list`]);
}
  getApprovalDetails(){

    this.apiService.getApprovalDetails(this.cardId)
    .subscribe((resp: any) => {
      this.buildformGroup(resp);

      console.log("data",resp);
     
    });
  }
  onKeyPress(event: any) {
    const number = /[0-9\+\-\ ]/;
    let inputCharacter = String.fromCharCode(event.charCode);
    if (event.keyCode != 8 && !number.test(inputCharacter)) {
      event.preventDefault();
      console.log("event:", event)
    }
  }

  getAccountCurrency(key: string) {
    this.api.getDropDownValues(key).subscribe((data) => {
      this.arrayAccountCurrency = data;
    });
  }

  getAccountType(key: string) {
    this.api.getDropDownValues(key).subscribe((data) => {
      this.arrayAccountType = data;
    });
  }
  
  getUserRecomendation(key: string) {
    this.api.getDropDownValues(key).subscribe((data) => {
      this.arrayUserRecomendation = data;
    });
  }

  getAccountBranchDetails() {
    //debugger
    this.api.getAccountBranch().subscribe((data) => {
      console.log("data:: AccountBranch ", data)
      this.arrayAccountBranch = data;
    });
  }

  getCardInitiationDetails(){
    this.apiService.getCardInitiationDetails(this.cardId)
    .subscribe((resp: any) => {
    console.log("data",resp);
    this.currency=resp.currency;  
    this.approvalForm.controls.accountCurrency.setValue(this.currency);
    });
  }


  keyPressAlphanumeric(event) {
    var inp = String.fromCharCode(event.keyCode);
    if (/[a-zA-Z0-9]/.test(inp)) {
      return true;
    } else {
      event.preventDefault();
      return false;
    }
  }
}
