import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { DialogService } from 'app/shared/services/dialogs/dialog.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { CardInitiationSummaryComponent } from './card-initiation/card-initiation-summary/card-initiation-summary.component';

@Component({
  selector: 'app-application-entry-stage',
  templateUrl: './application-entry-stage.component.html',
  styleUrls: ['./application-entry-stage.component.scss']
})
export class ApplicationEntryStageComponent implements OnInit {
  isCardPrefrenceDone:boolean=false
  CardInitiationScreenDone:boolean=false;
  isCardInitiationDone:boolean=false;
  isCardCustomerInformationScreen:boolean=false;
  isCardFinancialDetailsScreen:boolean=false;
  isCardAccountService:boolean=false;
  isCardApplicationStageDone:boolean=false;


  constructor(private router:Router,private dialogService : DialogService,private ls: LocalStoreService, public dialog: MatDialog) { }

  ngOnInit(): void {
    this.isCardApplicationStageDone=this.ls.getItem('CARD_APPLICATION_ENTRY_DONE');
    this.isCardPrefrenceDone=this.ls.getItem('card_prefrence_done');
    this.isCardInitiationDone=this.ls.getItem('CARD_INITIATION_DONE');
    this.isCardCustomerInformationScreen=this.ls.getItem('Card_Customer_Done');
    this.isCardFinancialDetailsScreen=this.ls.getItem('CARD_FINANCIAL_DONE');
    this.isCardAccountService=this.ls.getItem('CARD_ACC_SERVICE_DONE')
  }
  openCardInitiationSummary(){
    this.dialogService.CardInitiationSummary();
  }
  goToFinancialSummarry(){
    this.dialogService.CardFinancialDetailsSummary();
  }
  goToCustomerInfo(){
    this.dialogService.CardCustomerInfoSummary();
  }

  goToCradAccountService(){
    this.dialogService.cardAccountServiceViewSummary();
  }
  cardPrefrenceSummary(){
    this.dialogService.cardPrefrenceSummary();
  }

  openCardEntryStageViewSummary(){
    this.dialogService.cardAppEntryStageViewSummary();
  }
}
