import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DialogService } from 'app/shared/services/dialogs/dialog.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AccountService } from 'app/views/card-account/account.service';
@Component({


  selector: 'app-underwriting-stage',
  templateUrl: './underwriting-stage.component.html',
  styleUrls: ['./underwriting-stage.component.scss']
})
export class UnderwritingStageComponent implements OnInit {
  isCardCreditRatingDetails: boolean = false;
  isCardQualitativeScoreboard:boolean=false;
  cardId :any;
  constructor(private router:Router, private ls: LocalStoreService,private dialogService : DialogService,
  private accountService: AccountService,private cdr: ChangeDetectorRef) { }

  ngOnInit(): void {
    this.isCardQualitativeScoreboard=this.ls.getItem('CARD_QUALITATIVE_SCOREBOARD_DONE');
    this.isCardCreditRatingDetails = this.ls.getItem('CARD_CREDIT_RATING_DONE');
    this.accountService.refresh.subscribe((res: any) => {
        this.cardId = this.ls.getItem('cardId');
      //  this.loanAccountId = this.ls.getItem('loanAccountId');
        this.isCardQualitativeScoreboard=this.ls.getItem('CARD_QUALITATIVE_SCOREBOARD_DONE');
        this.isCardCreditRatingDetails = this.ls.getItem('CARD_CREDIT_RATING_DONE');
        this.cdr.markForCheck();
      });
  }

  viewSummary(){
    this.router.navigate([`cardaccount/underwritingStage/summary`]);
  }

  goToCreditRatingSummary(){
    this.dialogService.cardAccountcreditRatingSummaryDialog();
  }
  openCardQualitativeDialog(){
    this.dialogService.CardQualitativeSummaryDialog();
  }
}
