import { Component, OnInit } from "@angular/core";
import { FormArray, FormBuilder, FormGroup } from "@angular/forms";
import { MatDialogRef } from "@angular/material/dialog";
import { MatSnackBar } from "@angular/material/snack-bar";
import { Router } from "@angular/router";
import { ApiService } from "app/shared/services/api.service";
import { LocalStoreService } from "app/shared/services/local-store.service";

@Component({
  selector: 'app-intrest-details-summary',
  templateUrl: './intrest-details-summary.component.html',
  styleUrls: ['./intrest-details-summary.component.scss']
})
export class IntrestDetailsSummaryComponent implements OnInit {
  interestForm: FormGroup;
  savingForm: FormGroup;
  loanForm: FormGroup;
  accountType: any;
  accountId: any;
  resp: {
    intrestType: string;
    interestRateApplicable: string;
    marginIn: number;
    effectiveRate: number;
  }[];
  // loanAccountId: any;
  cardId: number;
  constructor(
    private ls: LocalStoreService,
    private snack: MatSnackBar,
    private dialogRef: MatDialogRef<IntrestDetailsSummaryComponent>,
    private api: ApiService,
    private router: Router,
    private formBuilder: FormBuilder
  ) { }

  ngOnInit(): void {
    this.cardId=this.ls.getItem('cardId');
    this.interestForm = this.formBuilder.group({
      interest: new FormArray([]),
    });
    this.savingForm = this.formBuilder.group({
      saving: new FormArray([]),
    });

    this.loanForm = this.formBuilder.group({
      loan: new FormArray([]),
    });

    this.api.cardInterestFetchById(this.cardId).subscribe((resp) => {
      console.log("resp from backend ", this.resp);
      this.resp = resp;
      this.onChangeInterest(this.resp.length);
      this.in.patchValue(this.resp);
    });
  }

  assignAccountType(accountType) {
    this.accountType = accountType;
  }

  onChangeInterest(e) {
    this.cardId=this.ls.getItem('cardId');
    const numberOfItems = e || 0;
    if (this.in.length < numberOfItems) {
      for (let i = this.in.length; i < numberOfItems; i++) {
        this.in.push(
          this.formBuilder.group({
            intrestType: [""],
            intrestRateApplicable: [""],
            marginIn: [""],
            effectiveRate: [""],
            cardId: [this.cardId],
            createdDate: [""],
            createdBy: [""],
            lastModifiedDate: [""],
            lastModifiedBy: [""],
          })
        );
      }
    } else {
      for (let i = this.in.length; i >= numberOfItems; i--) {
        this.in.removeAt(i);
      }
    }
  }
  onChangeSaving(e) {
    this.cardId=this.ls.getItem('cardId');
    const numberOfItems = e || 0;
    if (this.is.length < numberOfItems) {
      for (let i = this.is.length; i < numberOfItems; i++) {
        this.is.push(
          this.formBuilder.group({
            intrestType: [""],
            interestRateApplicable: [""],
            marginIn: [""],
            effectiveRate: [""],
            cardId: [this.cardId],
            createdDate: [""],
            createdBy: [""],
            lastModifiedDate: [""],
            lastModifiedBy: [""],
          })
        );
      }
    } else {
      for (let i = this.is.length; i >= numberOfItems; i--) {
        this.is.removeAt(i);
      }
    }
  }
  onChangeLoan(e) {
    this.cardId=this.ls.getItem('cardId');
    const numberOfItems = e || 0;
    if (this.iloan.length < numberOfItems) {
      for (let i = this.iloan.length; i < numberOfItems; i++) {
        this.iloan.push(
          this.formBuilder.group({
            intrestType: [""],
            interestRateApplicable: [""],
            marginIn: [""],
            effectiveRate: [""],
            cardId: [this.cardId],
            createdDate: [""],
            createdBy: [""],
            lastModifiedDate: [""],
            lastModifiedBy: [""],
          })
        );
      }
    } else {
      for (let i = this.iloan.length; i >= numberOfItems; i--) {
        this.iloan.removeAt(i);
      }
    }
  }
  // convenience getters for easy access to form fields
  get f() {
    return this.interestForm.controls;
  }
  get in() {
    return this.f.interest as FormArray;
  }
  get fs() {
    return this.savingForm.controls;
  }
  get is() {
    return this.fs.saving as FormArray;
  }
  get floan() {
    return this.loanForm.controls;
  }
  get iloan() {
    return this.floan.loan as FormArray;
  }

  goBack() {
      this.router.navigate([
        `/cardaccount/applicationEnrichment/list`,
      ]); 
  }

  onSubmit(form) {}
  edit() {
    this.dialogRef.close();
    this.api.setFlag({
      flag: true,
    });
    this.router.navigate([
      `/cardaccount/applicationEnrichment/intrest-details`,
    ]);
  }
  check(accountType) {
    if (accountType) {
      console.log(this.snack.open);
      this.snack.open("Intrest detail is empty", "Error", {
        duration: 4000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    }
  }


}
