import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-digitalsign-summary',
  templateUrl: './digitalsign-summary.component.html',
  styleUrls: ['./digitalsign-summary.component.scss']
})
export class DigitalsignSummaryComponent implements OnInit {
  cardId:any;
  accountInfo: any;
  imageUrl: any;
  constructor( private router: Router,private ls: LocalStoreService, private api: ApiService, private loader: AppLoaderService) { }

  ngOnInit(): void {
    
    console.log(this.cardId)
     this.cardId = this.ls.getItem('cardId');
     console.log(this.cardId)
     this.getDigitalsignByCardId(this.cardId);
    //  if(this.accountInfo){
    //   this.accountId=this.accountInfo.businessProductAccount.accountId
    //   }
    
  }
  getDigitalsignByCardId(cardId) {
    this.api.getDigitalsignByCardId(cardId).subscribe((resp) => {
      console.log(resp)
        console.log(resp[0].fileUrl);
       this.imageUrl = resp[resp.length-1].fileUrl
      this.loader.close();
    }, (err: any) => {
      // this.loader.close();
    }); 
  }

  

}
