import { ChangeDetectorRef, Component, OnInit } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { Router } from "@angular/router";
import { LocalStoreService } from "app/shared/services/local-store.service";
import { CardAccChargeDetSumComponent } from "./charge-details/card-acc-charge-det-sum/card-acc-charge-det-sum.component";
import { IntrestDetailsSummaryComponent } from "./intrest-details/intrest-details-summary/intrest-details-summary.component";
import { AccountService } from 'app/views/card-account/account.service';
@Component({
  selector: "app-application-enrichment",
  templateUrl: "./application-enrichment.component.html",
  styleUrls: ["./application-enrichment.component.scss"],
})
export class ApplicationEnrichmentComponent implements OnInit {
  isCardInterestDetails: boolean = false;
  isCardChargeDetails: boolean = false;
  isCardApplicationEnrichmentDone: boolean = false;
  cardId: any;

  constructor(
    private router: Router,
    public dialog: MatDialog,
    private ls: LocalStoreService,
    private accountService : AccountService, private cdr: ChangeDetectorRef
  ) {}

  ngOnInit(): void {
    this.cardId=this.ls.getItem('cardId');
    this.isCardInterestDetails = this.ls.getItem('CARD_INTEREST_DETAILS_DONE');
    this.isCardChargeDetails = this.ls.getItem('CARD_CHARGE_DETAILS_DONE');

    if (this.isCardInterestDetails && this.isCardChargeDetails) {
      this.isCardApplicationEnrichmentDone = true;
    }
  }

  goToInterestSummary() {
    const dialogRef = this.dialog.open(IntrestDetailsSummaryComponent, {
      data: { number: "9656849771", type: "Mobile Number" },
      disableClose: true,
    });
    dialogRef.afterClosed().subscribe((submit) => {});
  }

  goToChargeSummary() {
    const dialogRef = this.dialog.open(CardAccChargeDetSumComponent, {
      data: { number: "9656849771", type: "Mobile Number" },
      disableClose: true,
    });
    dialogRef.afterClosed().subscribe((submit) => {});
  }

  navigate() {
    this.router.navigate([`/cardaccount/applicationEnrichment/Summary`]);
  }
}
