  saveQualitativeScore(){
    let submitData: any = {};
    // console.log(this.no_of_applicants);
    // let numberOfApplicant = this.no_of_applicants;
    console.log(this.noOfapplicants);
    let numberOfApplicant = this.noOfapplicants;
    // submitData.loanAccountId=this.loanaccountID;  noOfapplicants
    submitData.cardId=this.cardId;  
    // submitData.loanAccountId = this.loanAccountId? this.loanAccountId : 999612;// loanAccountId: this.loanaccountID,
    submitData.numberOfApplicant = numberOfApplicant;
    submitData.scoreCardId = this.scoreCardId.value;
    submitData.loggedInUser = 123;
    submitData.accountType="cardOrigin";
    
    submitData.scoreCardMapping = this.scoreCardMappingArray;
    console.log(submitData);
    this.api.saveQualitativeScorecard(submitData).subscribe((resp) => {
      console.log("data",submitData.cardId);
      this.snack.open('Card Origination Qualitative Scoreboard Saved!', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
      // console.log(resp);
      this.isDataSaved = true;
      // this.ls.setItem('CARD_QUALITATIVE_SCOREBOARD_DONE', true);
      // this.ls.setItem('CARD_UNDERWRITING_DONE',true);
      // this.disableDoneBtn=false
      this.accountService.refresh.next();
    
    }, err => {
      this.snack.open('Server Error!', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
    })
    this.next();
  }

  next(){
    this.ls.setItem('CARD_QUALITATIVE_SCOREBOARD_DONE', true);
    this.ls.setItem('CARD_UNDERWRITING_DONE',true);
    this.accountService.refresh.next();
     this.router.navigate(["/cardaccount/assessmentStage/list"]);
  }

