import { CUSTOM_ELEMENTS_SCHEMA, Component, OnInit } from "@angular/core";
import { FormArray, FormBuilder, FormGroup } from "@angular/forms";
import { MatDialogRef } from "@angular/material/dialog";
import { MatSnackBar } from "@angular/material/snack-bar";
import { Router } from "@angular/router";
import { ApiService } from "app/shared/services/api.service";
import { LocalStoreService } from "app/shared/services/local-store.service";


@Component({
  selector: 'app-card-acc-charge-det-sum',
  templateUrl: './card-acc-charge-det-sum.component.html',
  styleUrls: ['./card-acc-charge-det-sum.component.scss']
})
export class CardAccChargeDetSumComponent implements OnInit {

  form1: Boolean;
  form2: Boolean;

  chargeFormIT: FormGroup;
  savingForm: FormGroup;
  accountId: any;
  // accountType: any;
  resp: { intrestType: string; amount: string; waiver: string }[];
  loanAccountId: any;
  accountType: String = "Home Loan";
  cardId: number;
  cardInfo: any;

  constructor(
    private snack: MatSnackBar,
    private dialogRef: MatDialogRef<CardAccChargeDetSumComponent>,
    private ls: LocalStoreService,
    private api: ApiService,
    private router: Router,
    private formBuilder: FormBuilder
  ) {}

  ngOnInit(): void {
    this.cardId=this.ls.getItem('cardId');
    console.log(this.cardId);
    
    if(this.cardInfo){
      this.loanAccountId = this.cardInfo.loanAccountId;
    }
    this.chargeFormIT = this.formBuilder.group({
      chargeIT: new FormArray([]),
    });

    // this.api
    //   .getFlexcubeDetails("chargeDetails", this.accountType)
    //   .subscribe((response) => {
    //     this.onChangeInterest(response.length);
    //     this.in.patchValue(response);
    //   });

  this.api.cardChargeFetchById(this.cardId).subscribe((resp) => {
    console.log("resp from backend ", resp);
    this.resp = resp;
    this.onChangeInterest(this.resp.length);
    this.in.patchValue(this.resp);
  });

// setTimeout(() => {
//   this.api.cardChargeFetchById(this.cardId).subscribe((resp) => {
//     console.log("resp from backend ", this.resp);
//     this.resp = resp;
//     this.onChangeInterest(this.resp.length);
//     this.in.patchValue(this.resp);
//   });
// },10);
    
  }

  onChangeInterest(e) {
    this.cardId=this.ls.getItem('cardId');
    const numberOfItems = e || 0;
    if (this.in.length < numberOfItems) {
      for (let i = this.in.length; i < numberOfItems; i++) {
        this.in.push(
          this.formBuilder.group({
            intrestType: [""],
            amount: [""],
            waiver: [false],
            cardId: [this.cardId],
            createdDate: [""],
            craetedBy: [""],
            lastModifiedDate: [""],
            lastModifiedBy: [""],
          })
        );
      }
    } else {
      for (let i = this.in.length; i >= numberOfItems; i--) {
        this.in.removeAt(i);
      }
    }
  }

  // convenience getters for easy access to form fields
  get f() {
    return this.chargeFormIT.controls;
  }
  get in() {
    return this.f.chargeIT as FormArray;
  }

  goBack() {
    this.router.navigate(["/cardaccount/applicationEnrichment/list"]);
  }

  onSubmit(form) {}
  edit() {
    // const amount = this.chargeFormIT.controls['chargeIT'].value.at(-1)['amount']
    // console.log({amount});
    this.dialogRef.close();
    this.router.navigate([`/cardaccount/applicationEnrichment/charge-details`]);
  }

  check(accountType) {
    if (accountType) {
      console.log(this.snack.open);
      this.snack.open("charge detail is empty", "Error", {
        duration: 4000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    }
  }

  close(){
    this.router.navigate(["/cardaccount/applicationEnrichment/list"]);
  }

  _keyPress(event: any) {
    const pattern = /[a-zA-Z]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
}
