import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { ApplicationEntryStageService } from 'app/shared/services/application-entry-stage.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import * as moment from 'moment';
import { AccountService } from 'app/views/card-account/account.service';


@Component({
  selector: 'app-card-initiation',
  templateUrl: './card-initiation.component.html',
  styleUrls: ['./card-initiation.component.scss']
})
export class CardInitiationComponent implements OnInit {
  cifNumberForm = new FormControl();
  timer: any;
  listOfCustByCif: any = [];

  cardInitiationForm: FormGroup;
  showEditOption: boolean = false;
  isCardInitiationDone:boolean = false;
  arrayofEmployment: any[] = [];
  cardId: any;
  applicant: any;
  console: any;
  isCifValid: boolean = false;
  onbackSpace: boolean = false;
  showSpinn: boolean;
  cifNumberPrimary: any;
  flag: boolean;
  currencyData: any;
  arrayAccountBranch: any;
  branchName: any;
  isExistingCustomer: boolean = true;
  isAddOnCard:boolean = false;
  localCurrencyArr = ['CNY', 'GBP', 'INR', 'USD', 'ZAR', 'GBP'];
  selectedCurrency = this.localCurrencyArr[-1];
  kycStatus: any;
  status: any;
  iskycStatus: boolean = false;
  isGoForKyc: boolean = false;
  isKycForCardDone: boolean = false;
  cardType: any;
  kycRefrence: string;
  accountID: any;
  accountNoArr = [];
  cifNumber: any;
  createdBy: any;
  constructor(private ls: LocalStoreService, private router: Router, private accountService: AccountService,
    private formBuilder: FormBuilder, private apiService: ApiService, private appApiService: ApplicationEntryStageService,
    private snack: MatSnackBar, private cdr: ChangeDetectorRef) {
  }

  ngOnInit(): void { 
   // this.ls.getItem("kycnumber");
   console.log(this.ls.getItem("cardCustomer"));
    this.cardId=this.ls.getItem('cardId');
    this.applicant=this.ls.getItem('NAME');
    this.getEmploymentType("employment_type");
    this.createdBy= this.ls.getItem("ICUST_USER");
    
    
    console.log("this.cardId",this.cardId);

    this.buildInitiationForm();
    // this.cardInitiationForm.get('applicantName').setValue(this.applicant);
    let isCardDetails: boolean = JSON.parse(this.ls.getItem('CARD_INITIATION_DONE'));
    this.isCardInitiationDone = this.ls.getItem('CARD_INITIATION_DONE');
    if (this.cardId != null && this.isCardInitiationDone) {
      this.iskycStatus = true;
      this.showEditOption = true;
      this.cifNumberPrimary = this.ls.getItem("Card_CIF_NUM_PRIMARY");
      this.getCardInitiation();

    }


    //console.log("this.kycStatus",this.kycStatus);
    this.isKycForCardDone = this.ls.getItem("kycForCard");
    if (this.isKycForCardDone == true) {
      // this.kycStatus = this.ls.getItem("KycStatus");
      this.cardInitiationForm.get('existingCustomer').patchValue(false);
      this.kycRefrence = this.ls.getItem("kycReference");
      if(this.kycRefrence){
        let kycNum = this.kycRefrence.slice(4);
        console.log(kycNum);        
        this.apiService.getPrimaryInfo(kycNum).subscribe((result: any) => {
          this.kycStatus = result.customerInfoList[0].customerAdditionalInfo.kycStatus
          this.ls.setItem('KycStatus',this.kycStatus)
        })
      }
      this.iskycStatus = true;
      this.isGoForKyc = true;
      this.cardInitiationForm.get('cardType').patchValue(this.ls.getItem("cardType"));
      console.log(this.ls.getItem("cardCustomer"));
      this.cardInitiationForm.controls.applicantName.setValue(this.ls.getItem("cardCustomer"));
      //this.ls.getItem("cardType");
    }
    else {
      //   this.iskycStatus=false;
    }
  }

  onKeyPress(event: any) {
    if (event.keyCode === 8) {
      this.onbackSpace = true;
      this.isCifValid = false;
    }
    else {
      this.onbackSpace = false;
    }
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
    console.log(event.target.value, !event.target.value);
    clearTimeout(this.timer)
    
    if (!event.target.value) {
      this.isCifValid = false;
      this.cardInitiationForm.get('cifNumber').clearValidators();
      this.cardInitiationForm.get('cifNumber').updateValueAndValidity();
      this.cardInitiationForm.get('existingCustomer').patchValue(true);
      this.cardInitiationForm.reset();
      this.listOfCustByCif = []
    } else {
      this.timer = setTimeout(() => {
        this.appApiService.fetchByCif(event.target.value).subscribe((res: any) => { 
          console.log(res);
          if (res) {
            this.listOfCustByCif = res.customerInfoList
          }
        })
      }, 1000);
    }
  }

  goToggle(event: any) {
    // this.cardInitiationForm.reset();
    console.log("event", event.checked);
    if (event.checked == true) {
      // this.cardInitiationForm.reset();
      this.cardInitiationForm.get('existingCustomer').patchValue(true);
      this.isExistingCustomer = true;
      // this.cardInitiationForm.reset();

    }
    else {
      // this.cardInitiationForm.reset();
      this.cardInitiationForm.controls.currency.setValue("");
      // this.cardInitiationForm.get("currency").setValue("");    
      this.cardInitiationForm.get("employmentType").setValue("");
      this.cardInitiationForm.get("applicantName").setValue("");
      this.cardInitiationForm.get("customerAccount").setValue("");
      this.cardInitiationForm.get("nameOnCard").setValue("");
      this.cardInitiationForm.get("affinityProgram").setValue("");
      this.cardInitiationForm.get("cifNumber").setValue("");
      this.kycRefrence = '';
      this.kycStatus = '';
      this.iskycStatus= false
      this.cardInitiationForm.get('existingCustomer').patchValue(false);
      this.isExistingCustomer = false;
      
    }

  }
  addOnCard(event: any) {
    console.log("event", event.checked);
    if (event.checked == true) {
      this.isAddOnCard = true;
      this.ls.setItem('ADD_ON_CARD', true);
    }
    else {
      this.isAddOnCard = false;
      this.ls.setItem('ADD_ON_CARD', false);
    }

  }
  
  getEmploymentType(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayofEmployment = data;
      console.log(this.arrayofEmployment);

    });
  }

  getAccountData(cifNumber){
    this.apiService.getProfileDetails(cifNumber)
      .subscribe((data: any) => {
        console.log(data);
        console.log(data?.signatures?.[0]?.accountId)
        this.accountID = data?.signatures?.[0]?.accountId;
        this.cardInitiationForm.controls.currency.setValue(data?.accountCurrency);
      }, (error: any) => {
        // console.log(error);
      });
  }
  onSubmit() {
    console.log("data", this.cardInitiationForm.value)
    localStorage.setItem('Currency Type', this.cardInitiationForm.value.currency);
    let payload = {
      existingCustomer: this.cardInitiationForm.value.existingCustomer,
      cardType: this.cardInitiationForm.value.cardType,
      customerAccount: this.cardInitiationForm.value.customerAccount,
      applicantName: this.cardInitiationForm.value.applicantName,
      currency: this.cardInitiationForm.value.currency,
      employmentType: this.cardInitiationForm.value.employmentType,
      affinityProgram: this.cardInitiationForm.value.affinityProgram,
      nameOnCard: this.cardInitiationForm.value.nameOnCard,
      status: "ONGOING",
      kycStatus: this.kycStatus,
      kycReference: this.kycRefrence,
      customerId: this.cardInitiationForm.value.cifNumber,

      cardId: this.showEditOption ? this.cardId : null,
      createdBy: this.createdBy.userName
    }

    this.apiService.uploadCardInitiation(payload)
      .subscribe((data: any) => {
        this.ls.setItem('cardId', data.cardId);
        let statusModel = {
          cardId: data.cardId,
          status: "ONGOING"
        };
        // this.apiService.updateCardStatus(statusModel).subscribe(data => {
        // });

        console.log("data", data);
        
        this.next();
      });

  }
  next() {
    this.ls.setItem('CARD_INITIATION_DONE',true);
    this.ls.setItem('Card_Applicant_name',this.cardInitiationForm.value.applicantName);
    this.accountService.refresh.next();
    this.router.navigate([`/cardaccount/applicationEntryStage/list/customerInformation`]);
  }
  getCardInitiation() {

    this.apiService.getCardInitiationDetails(this.cardId)
    .subscribe((resp: any) => {
      this.kycStatus = resp.kycStatus;
      this.kycRefrence = resp.kycReference;
      this.buildInitiationForm(resp);
      this.apiService.fetchAccountIdByCustId(this.cifNumberPrimary).subscribe((accountNo: any[]) => {
        this.accountNoArr = accountNo;
        this.cardInitiationForm.controls.customerAccount.setValue(this.accountNoArr?.[0])
      });
      console.log("data", resp);
      if (resp.existingCustomer == true) {
        this.isExistingCustomer = true;
      }
      else {
        this.isExistingCustomer = false;
      }

    });
  }
  buildInitiationForm(data?) {
    const addOnCard = localStorage.getItem("ADD_ON_CARD"); 
        
    
    this.cardInitiationForm = this.formBuilder.group({
      cifNumber: [data ? this.cifNumberPrimary : '',data?.existingCustomer == true?[Validators.required]:[Validators.nullValidator]],
      existingCustomer: [data ? data.existingCustomer : true,],
      cardType: [data ? data.cardType : '', [Validators.required]],
      customerAccount: [data ? data.customerAccount : '', data?.existingCustomer == true?[Validators.required]:[Validators.nullValidator]],
      applicantName: [data ? data.applicantName : ''],
      currency: [data ? data.currency : '', [Validators.required]],
      employmentType: [data ? data.employmentType : '', [Validators.required]],
      affinityProgram: [data ? data.affinityProgram : '', [Validators.required]],
      nameOnCard: [data ? data.nameOnCard : '', [Validators.required]],
      addOnCard:[addOnCard == 'true' ? true : false],
    });
    this.selectedCurrency = data?.currency;
    this.formControlValueChanged();
  }
  formControlValueChanged() {
    
    let cifNumberControl = this.cardInitiationForm.get('cifNumber');
    this.cardInitiationForm.get('existingCustomer').valueChanges.subscribe(
        (val) => {
            console.log(val);
            if (val == false) {
              this.cardInitiationForm.get('customerAccount').setValidators([Validators.nullValidator]);
                cifNumberControl.setValidators([Validators.nullValidator]);
            }   
            else{
              this.cardInitiationForm.get('customerAccount').setValidators([Validators.required]);
              cifNumberControl.setValidators([Validators.required]);
          }            
            cifNumberControl.updateValueAndValidity();
        });
}
  goBack() {
    this.router.navigate([`/cardaccount/applicationEntryStage/list`]);
  }
  keyUpevent(event) {
    if (event.keyCode === 8) {
      this.onbackSpace = true;
      this.isCifValid = false;
    }
    else {
      this.onbackSpace = false;
    }
  }
  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
  letterOnly(event) {
    console.log(!this.isGoForKyc);
    console.log(!this.cardInitiationForm.valid);
    console.log(!this.cardInitiationForm.valid || !this.isGoForKyc);
    
    console.log(this.cardInitiationForm);
    var charCode = event.keyCode;
    if ((charCode > 64 && charCode < 91) || (charCode > 96 && charCode < 123) || charCode == 8)
      return true;
    else
      return false;
  }

  fetchByCif(cifNumber) {

    if (!!cifNumber) {
      this.showSpinn = true;
      this.appApiService.fetchByCif(cifNumber).subscribe((res: any) => {
        this.kycStatus = res.customerInfoList[0].customerAdditionalInfo.kycStatus
        this.kycRefrence = res.customerInfoList[0].customerAdditionalInfo.kycReference
     
        this.cardInitiationForm.controls.cifNumber.setValue(cifNumber);
        this.cardInitiationForm.controls.applicantName.setValue(res.customerInfoList[0].firstName + " " + res.customerInfoList[0].lastName);
       // this.ls.setItem('Card_Applicant_name', res.customerInfoList[0].firstName + " " + res.customerInfoList[0].lastName);
        this.cardInitiationForm.controls.customerAccount.setValue(this.accountID)

        this.showSpinn = false;

        if (!res) {
          this.isCifValid = false;

          this.snack.open('Invalid CIF Number', 'OK', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });
          // this.cardInitiationForm.get('cifNumber').patchValue("");
          return;
        } else {
          if (res.customerInfoList[0].custStatus != "APPROVED") {
            this.isCifValid = false;
          }
          if (res.customerInfoList[0].custStatus == "APPROVED") {
            this.isCifValid = true;
          }
          //console.log(this.isCifValid,"else2 condition");

          let address: any;
          // this.console.log(res.customerInfoList[0].userAddress.length);
          if (res.customerInfoList[0].userAddress.length > 1) {
            address = res.customerInfoList[0].userAddress.find((item: any) => item.addressType == "Permanent" || item.addressType == "permanent");
          } else {
            address = res.customerInfoList[0].userAddress[0];
          }
          let queryParams = {
            prefix: res.customerInfoList[0].prefix,
            customerId: res.customerInfoList[0].customerId,
            firstName: res.customerInfoList[0].firstName,
            middleName: res.customerInfoList[0].middleName,
            lastName: res.customerInfoList[0].lastName,
            gender: res.customerInfoList[0].gender,
            dateOfBirth: this.convertDate(res.customerInfoList[0].dateOfBirth),
            placeOfBirth: res.customerInfoList[0].placeOfBirth,
            residenceStatus: res.customerInfoList[0].residenceStatus,
            nationality: res.customerInfoList[0].nationality,
            countryOfResidence: res.customerInfoList[0].countryOfResidence,
            occupationType: res.customerInfoList[0].occupationType,
            custIdProofs: res.customerInfoList[0].custIdProofs,
            idType: res.customerInfoList[0].custIdProofs.length > 0 ? res.customerInfoList[0].custIdProofs[0].idType : '',
            idNumber: res.customerInfoList[0].custIdProofs.length > 0 ? res.customerInfoList[0].custIdProofs[0].idNumber : '',
            expiryDate: res.customerInfoList[0].custIdProofs.length > 0 ? this.convertDate(res.customerInfoList[0].custIdProofs[0].expiryDate) : '',
            addressType: address.addressType,
            address1: address.address1,
            address2: address.address2,
            city: address.city,
            country: address.country,
            state: address.state,
            zipCode: address.zipCode,
            phoneNumber: res.customerInfoList[0].phoneNumber,
            landlineNumber: res.customerInfoList[0].landlineNumber,
            primaryEmailAdress: res.customerInfoList[0].primaryEmailAdress
          };
          if (this.cardInitiationForm.value.swiftCode == null || this.cardInitiationForm.value.swiftCodeEnable == null) {
            console.log(this.cardInitiationForm.value.swiftCode);
            this.flag = false;
          }
          this.cifNumberPrimary = cifNumber;
          this.ls.setItem("Card_CIF_NUM_PRIMARY", this.cifNumberPrimary);
          this.apiService.sendCIF(queryParams);
          this.apiService.fetchAccountIdByCustId(cifNumber).subscribe((accountNo: []) => {
            this.accountNoArr = accountNo;
            this.cardInitiationForm.controls.customerAccount.setValue(this.accountNoArr?.[0])
            console.log(accountNo);
          });
        }
        this.cdr.markForCheck();

      });
      this.getAccountData(cifNumber);
    }
  }
  changeEvent(event: any) {
    console.log(event.value);
    this.cardType = event.value
    if (event.value == "Credit" || "Debit" || "Forex") {
      this.iskycStatus = true;
      if (this.kycStatus == "APPROVED") {
        this.isGoForKyc = true;
      }
      else {
        this.isGoForKyc = false;
      }

    }
    else {
      this.isGoForKyc = true;
      this.iskycStatus = false;
    }
  }
  convertDate(value) {
    let date = moment(value).format('YYYY-MMM-DD'); // ISO 8601 format
    // let date = new Date(value).format('yyyy-MMM-dd');
    if (date)
      return date;
  }
  goToKyc() {
    this.ls.setItem('cardType', this.cardType);
    this.ls.setItem("accountType", 'cardOrigination')
    this.router.navigate([`/others/kyc`]);
  }

}
