  toggleChanges() {
    if (this.applicant.get('existingCustomer').value == true) {
      this.applicant.get('cifNumber').enable();
      this.applicant.get('cifNumber').setValidators([Validators.required]);
      this.applicant.get('cifNumber').updateValueAndValidity();
    }
    else {
      this.applicant.patchValue({ cifNumber: '' })
      this.isDuplicateCIF = false;
      this.applicant.get('cifNumber').disable();
      this.applicant.get('cifNumber').clearValidators();
      this.applicant.get('cifNumber').updateValueAndValidity();
    }
  }

