  onKeyPress1(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    } console.log(event.target.value);
    clearTimeout(this.timer)
    if (!event.target.value) {
      this.listOfCustByCif = []
    }
  }

  onKeyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
    console.log(event.target.value);
    clearTimeout(this.timer)
    if (!event.target.value) {
      this.isCifValid = false;
      this.applicant.reset();
      this.applicant.get('cifNumber').clearValidators();
      this.applicant.get('cifNumber').updateValueAndValidity();
      this.applicant.get('existingCustomer').patchValue(true);
      this.applicant.get('primaryCustomer').patchValue(true);
      this.listOfCustByCif = []
    } else {
      this.timer = setTimeout(() => {
        this.appApiService.fetchByCif(event.target.value).subscribe((res: any) => {
          console.log(res);
          if (res) {
            this.listOfCustByCif = res.customerInfoList
          }
        })
      }, 1000);
    }
  }

  isAllSameDigit(event) {
    let count = 0, number = event.target.value;
    if (number != '') {
      for (var i = 0; i < number.length; i++) {
        if (0 == number[i]) {
          count++;
        }
      }
      console.log('number :: ', number);
      console.log('isAllSameDigitVal :: ', count, number.length)
      if (count == number.length) {
        console.log('if loop')
        this.isAllSameDigitVal = true;
      }
      else {
        this.isAllSameDigitVal = false;
      }
    } else {
      this.isAllSameDigitVal = false;
    }
  }

