  goToggle(event: any) {
    console.log("event", event.checked);
    this.addonCardValue = event.checked
    // if(event.checked==true){
    // this.ls.setItem('addonCardApplicant', 2);
    // }
    if (event.checked == true || this.addOnCard) {
      this.ls.setItem('ADD_ON_CARD', true);
      this.cardCustomerForm.get('ownership').setValue('Joint');
      this.cardCustomerForm.get('numOfApplicants').patchValue(2);
      this.disabled = false;
    }
    else if (event.checked == false) {
      this.ls.setItem('ADD_ON_CARD', false);
      this.cardCustomerForm.get('ownership').patchValue('Self', { onlySelf: true, emitEvent: false });
      this.cardCustomerForm.get('numOfApplicants').patchValue(1, { onlySelf: true, emitEvent: false });
    }

  }

  getCityandStateByZipcode(countryName, zipcode) {
    if (countryName && zipcode.length >= 6) {
      this.apiService.fetchStateCityByZipcode(countryName, zipcode).subscribe((res: any) => {
        if(res){
          this.applicant.get('state').setValue(res.stateName);
          this.applicant.get('city').setValue(res.cityName)
        }
        else{
          this.snack.open("Entered Zipcode does not exists", "OK", {
            duration: 4000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });
        }
      })
    }
  }
