import { Component, OnInit } from "@angular/core";
import { MatDialog, MatDialogRef } from "@angular/material/dialog";
import { Router } from "@angular/router";
import { ApiService, eSign_URL } from "app/shared/services/api.service";
import { ApplicationEntryStageService } from "app/shared/services/application-entry-stage.service";
import { LocalStoreService } from "app/shared/services/local-store.service";
import { AccountService } from "app/views/card-account/account.service";
import { UrlService } from "app/views/home/url.service";
import { EsignaturePopComponent } from "../esignature-pop/esignature-pop.component";
import { UploadSignatureOrEsignComponent } from "../upload-signature-or-esign/upload-signature-or-esign.component";
import { DialogService } from "app/shared/services/dialogs/dialog.service";
import { rumangoAnimations } from "app/shared/animations/rumango-animations";
@Component({
  selector: "app-digital-sign-now-or-later",
  templateUrl: "./digital-sign-now-or-later.component.html",
  styleUrls: ["./digital-sign-now-or-later.component.scss"],
  animations: rumangoAnimations
})
export class DigitalSignNowOrLaterComponent implements OnInit {
  accountdetailsForStatusUpdate: Object;
  previousUrl: string = "";
  show: boolean = false;
  show1: any;
  cardId:any;
  accountId: any;
  accountID:any;
  isDone: boolean = false;

  constructor(
    private dialog: MatDialog,
    private router: Router,
    private ls: LocalStoreService,
    private accountService: AccountService,
    private apiService: ApplicationEntryStageService,
    private urlService: UrlService,
    private api: ApiService,
    private dialogService: DialogService
  ) {}

  ngOnInit(): void {
    this.cardId = this.ls.getItem("cardId");
    this.show1 = this.ls.getItem('eSignStage') && (this.ls.getItem('signedImage') !== null)
    this.urlService.previousUrl$.subscribe((previousUrl: string) => {
      this.previousUrl = previousUrl;
    });
  }

  openPopUp(data?: any) {
    let response: any;
    if (data === undefined || data === null) {
      response = {
        isSignLater: false,
        screen: "Create Account",
      };
    }
    if (!this.show) {
      let dialogRef: MatDialogRef<any> = this.dialog.open(
        UploadSignatureOrEsignComponent,
        {
          width: "720px",
          disableClose: true,
          data: data === undefined || data === null ? response : data,
        }
      );
      dialogRef.afterClosed().subscribe((res) => {
        this.accountId = this.accountID;
        console.log(res);
        if(res){
          this.show=this.ls.getItem("enable");
          // this.show = this.ls.getItem('eSignStage')
          this.show1=this.ls.getItem("enable");

          console.log(this.show);
          this.getByAccountId(this.accountId);
        }
        // this.show = true;       
          
        //    this.ls.setItem("enable", true);
        //   this.getByAccountId(this.accountId);

      });
    }
  }
  getByAccountId(accountId: any) {
    this.api.getCardInitiationDetails(this.cardId).subscribe((resp) => {
      this.accountdetailsForStatusUpdate = resp;
      this.updateCardAccountStatus();
    });
  }
  onDone() {
    this.isDone = true;
    this.dialogService.alertForCardFinishButton().subscribe((response) => {
      console.log("response ::", response);
    });
  }
  updateCardAccountStatus() {
    let statusModel = {
      cardId: this.cardId,
      status: "BACKLOG",
    };
    this.api.updateCardStatus(statusModel).subscribe(updateResp => {
      console.log("updateResp:: ", updateResp);
    });
  }

  goBack() {
    this.router.navigate(['/cardaccount/DigitalSigning/list']);
  }

  resetState() {
    this.ls.removeItem("CARD_APPLICATION_ENTRY_DONE");
    this.ls.removeItem("CARD_INITIATION_DONE");
    this.ls.removeItem("Card_Customer_Done");
    this.ls.removeItem("CARD_FINANCIAL_DONE");
    this.ls.removeItem("card_prefrence_done");
    this.ls.removeItem("CARD_ACC_SERVICE_DONE");
    this.ls.removeItem("Card_CIF_NUM_PRIMARY");
    this.ls.removeItem("Card_Applicant_name");

    this.ls.removeItem("CARD_DOCUMENT_STAGE_DONE");
    this.ls.removeItem("CARD_DOCUMENT_DONE");

    this.ls.removeItem("CARD_ENRICHMENT_DONE");
    this.ls.removeItem("CARD_INTEREST_DETAILS_DONE");
    this.ls.removeItem("CARD_CHARGE_DETAILS_DONE");

    this.ls.removeItem("CARD_UNDERWRITING_DONE");
    this.ls.removeItem("CARD_CREDIT_RATING_DONE");
    this.ls.removeItem("CARD_QUALITATIVE_SCOREBOARD_DONE");

    this.ls.removeItem("CARD_ASSESSMENT_STAGE_DONE");
    this.ls.removeItem("CARD_ASSESSMENT_DONE");

    this.ls.removeItem("CARD_SUPERVISOR_DONE");
    this.ls.removeItem("CARD_APPROVAL_DONE");

    this.ls.removeItem("CARD_DIGITAL_STAGE_DONE");
    this.ls.removeItem("CARD_DIGITAL_SIGN_DONE");
    this.ls.removeItem("enable");
    this.ls.removeItem("eSignStage");
    this.ls.removeItem("cardId");
    this.ls.removeItem("CardNoOfApplicants");
    this.ls.removeItem("primaryCustomerName");
    this.ls.removeItem("cardownership_value");
    this.ls.removeItem("cId");
    this.ls.removeItem("cardSignedImage");
    this.ls.removeItem("enable")

    sessionStorage.clear();
    this.accountService.refresh.next();
    this.router.navigate(["/cardaccount/applicationEntryStage/list"]);
  }

  onSubmit() {
    let payload = {
      cardId: this.cardId,
      esignLink: "",
      subject: "testing",
      
    };
    this.api.signInLater(payload).subscribe(
      (data: any) => {
        console.log("data::", data);
        this.getByAccountId(this.cardId);
        this.ls.setItem("eSignStage", true);
        // this.accountService.refresh.next();
        let response: any = {
          email: data.email,
          customerId: data.customerId,
          subject: data.subject,
          isSignLater: true,
          screen: "Card Account",
        };
        this.isDone=true;
        this.dialogService.alertForcardSignLater().subscribe((response) => {
          console.log("response ::", response);                 
        });  
        this.show = this.ls.setItem("enable",true);
        //this.openPopUp(response);
      },
      (err: any) => {}
    );
  }
}
