import { Component, OnInit } from "@angular/core";
import { FormArray, FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatSnackBar } from "@angular/material/snack-bar";
import { Router } from "@angular/router";
import { ApiService } from "app/shared/services/api.service";
import { LocalStoreService } from "app/shared/services/local-store.service";
// import { AccountService } from "app/views/loan-account/account.service";
import { AccountService } from 'app/views/card-account/account.service';


@Component({
  selector: "app-charge-details",
  templateUrl: "./charge-details.component.html",
  styleUrls: ["./charge-details.component.scss"],
})
export class ChargeDetailsComponent implements OnInit {
  interestForm: FormGroup;
  accountId: any;
  intrestFlag: Boolean;
  interestResp: any;
  accountType: String = "Card Origin";
  resp: { intrestType: string; amount: string; waiver: string }[];
  loanaccountID: any;
  cardId: number;
  currencytype:any;
  currencySymbol: string;
  cardInfo: any;
  constructor(
    private accountService: AccountService,
    private snack: MatSnackBar,
    private ls: LocalStoreService,
    private api: ApiService,
    private router: Router,
    private formBuilder: FormBuilder
  ) {}

  ngOnInit(): void {
    console.log(history.state.amount);
    
    this.cardId = this.ls.getItem('cardId');
    this.currencytype=localStorage.getItem('Currency Type');
    if(this.currencytype==="INR"){
      this.currencySymbol=""
    }else if(this.currencytype==="USD"){
      this.currencySymbol="$"
    }else if(this.currencytype==="CNY"){
      this.currencySymbol=""
    }else if(this.currencytype==="GBP"){
      this.currencySymbol=""
    }else if(this.currencytype==="ZAR"){
      this.currencySymbol="R"
    }

    if(this.cardInfo){
      this.loanaccountID = this.cardInfo.loanAccountId;
    }
    this.interestForm = this.formBuilder.group({
      chargeInfo: new FormArray([]),
    });
    this.api
      .getFlexcubeDetails("chargeDetails", this.accountType)
      .subscribe((response) => {
        this.onChangeInterest(response.length);
        this.in.patchValue([{...response[0], amount: history.state.amount ? history.state.amount : ''}]);
      });    
setTimeout(()=>{
  this.api.cardChargeFetchById(this.cardId).subscribe((resp) => {
    console.log("resp from backend ", this.resp);
    this.resp = resp;
    this.onChangeInterest(this.resp.length);
    this.in.patchValue(this.resp);
  });
},10);
    
  }

  onChangeInterest(e) {
    this.cardId=this.ls.getItem('cardId');
    const numberOfItems = e || 0;
    if (this.in.length < numberOfItems) {
      for (let i = this.in.length; i < numberOfItems; i++) {
        this.in.push(
          this.formBuilder.group({
            intrestType: [""],
            amount: ["",Validators.required],
            waiver: [false],
            cardId: [this.cardId],
            chargeDetailsId: [""],
          })
        );
      }
    } else {
      for (let i = this.in.length; i >= numberOfItems; i--) {
        this.in.removeAt(i);
      }
    }
  }

  get f() {
    return this.interestForm.controls;
  }
  get in() {
    return this.f.chargeInfo as FormArray;
  }

  goBack() {
    this.router.navigate([
      `/cardaccount/applicationEnrichment/intrest-details`,
    ]);
  }
  appendPercentage(event) {
    // console.log(event.key)
    console.log(event.target.value);

    if (!event.target.value.length) return;

    if (event.target.value.length === 1) {
      event.target.value = `${event.target.value}%`;
    }
    if (event.target.value.length > 1) {
      event.target.value = `${event.target.value.slice(0, -1)}%`;
    }
  }

  onSubmit(form) {
    console.log("form", form);
    form.chargeInfo = form.chargeInfo.map((chargeInfo) => ({
      ...chargeInfo,
      // loanAccountId: this.loanaccountID,
    }));

    this.api.cardChargeDetailsSave(form).subscribe(
      (resp) => {
        this.snack.open("Charge Details Saved!", "OK", {
          duration: 4000,
          verticalPosition: "top",
          horizontalPosition: "right",
        });
        this.next();
        console.log("backend response from intrest saving", resp);
      },
      (err) => {
        this.snack.open(
          "Not able to connect to server!",
          "INTERNAL SERVER ERROR",
          {
            duration: 4000,
            verticalPosition: "top",
            horizontalPosition: "right",
          }
        );
      }
    );
  }
  check(accountType) {
    if (accountType) {
      console.log(this.snack.open);
      this.snack.open("chargeInfo detail is empty", "Error", {
        duration: 4000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    }
  }
  checkbox(event) {
    console.log(event.checked);
  }
  next() {
    this.ls.setItem("CARD_CHARGE_DETAILS_DONE", true);
    this.ls.setItem('CARD_ENRICHMENT_DONE',true);
    this.accountService.refresh.next();
    this.router.navigate([`/cardaccount/underwritingStage/list`]);
  }

  onKeyPress(event: any) {
    const number = /[0-9\+\-\ ]/;
    let inputCharacter = String.fromCharCode(event.charCode);
    if (event.keyCode != 8 && !number.test(inputCharacter)) {
      event.preventDefault();
      console.log("event:", event)
    }
  }
}
