import { Component, Input, OnInit } from "@angular/core";
import { FormArray, FormBuilder, FormGroup } from "@angular/forms";
import { MatSnackBar } from "@angular/material/snack-bar";
import { Router } from "@angular/router";
import { ApiService } from "app/shared/services/api.service";
import { LocalStoreService } from "app/shared/services/local-store.service";

@Component({
  selector: "app-enrichment-summary",
  templateUrl: "./enrichment-summary.component.html",
  styleUrls: ["./enrichment-summary.component.scss"],
})
export class EnrichmentSummaryComponent implements OnInit {
  appEntrySummaryView: boolean = false;
  accountDetails: any;
  interestForm: FormGroup;
  chargeFormIT: FormGroup;
  savingForm: FormGroup;
  loanForm: FormGroup;
  interestForm1: FormGroup;
  accountId: any;
  //for loan interest details
  loanInterestResp: {
    intrestType: string;
    interestRateApplicable: string;
    marginIn: number;
    effectiveRate: number;
  }[];
  intererstInfo: {
    intrestType: string;
    interestRateApplicable: string;
    margin: number;
    effectiveRate: number;
  }[];
  accountType: String = "Home Loan";
  cardId:any;
  chargeInfo: any;
  constructor(
    private formBuilder: FormBuilder,
    private ls: LocalStoreService,
    private snack: MatSnackBar,
    private api: ApiService,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.cardId=this.ls.getItem('cardId');
    // this.api.getApprovedStatus().subscribe((resp)=>{
    //   console.log("message",resp);
    //   this.isApprovedSreen=resp;

    // })
    // this.loanAccountId = this.ls.getItem('loanAccountId');
    this.getCardEnrichmentDetails(this.cardId);
    this.interestForm = this.formBuilder.group({
      interest: new FormArray([]),
    });
    this.savingForm = this.formBuilder.group({
      saving: new FormArray([]),
    });
    this.chargeFormIT = this.formBuilder.group({
      chargeIT: new FormArray([]),
    });
    this.loanForm = this.formBuilder.group({
      loan: new FormArray([]),
    });
    
   

    //for Charge Details
    // this.api
    //   .getFlexcubeDetails("chargeDetails", this.accountType)
    //   .subscribe((response) => {
    //     this.onCardCharge(response.length);
    //     this.in2.patchValue(response);
    //   });
  }

  getCardEnrichmentDetails(cardId:any){
    
    this.api.getCardEnrichmentSummmary(cardId)
      .subscribe((summaryInfo: any) => {
       console.log(summaryInfo);
       this.intererstInfo=summaryInfo.intererstInfo;
       console.log(this.intererstInfo);
       this.onCardintrest(this.intererstInfo.length);
       this.in.patchValue(this.intererstInfo);
       
       this.chargeInfo = summaryInfo.chargeInfo;
       this.onCardCharge(this.chargeInfo.length);
       this.in2.patchValue(this.chargeInfo);
      });
  }

  onCardintrest(e) {
    // this.cardId=this.ls.getItem('cardId');
    const numberOfItems = e || 0;
    if (this.in.length < numberOfItems) {
      for (let i = this.in.length; i < numberOfItems; i++) {
        this.in.push(
          this.formBuilder.group({
            intrestType: [""],
            intrestRateApplicable: [""],
            marginIn: [""],
            effectiveRate: [""],
            //accountId: [this.accountId],
            cardId: [this.cardId],
            createdDate: [""],
            createdBy: [""],
            lastModifiedDate: [""],
            lastModifiedBy: [""],
          })
        );
      }
    } else {
      for (let i = this.in.length; i >= numberOfItems; i--) {
        this.in.removeAt(i);
      }
    }
  }

  onCardCharge(e) {
    this.cardId=this.ls.getItem('cardId');
    const numberOfItems = e || 0;
    if (this.in2.length < numberOfItems) {
      for (let i = this.in2.length; i < numberOfItems; i++) {
        this.in2.push(
          this.formBuilder.group({
            intrestType: [""],
            amount: [""],
            waiver: [true],
            cardId: [this.cardId],
            chargeDetailsId: [""],
          })
        );
      }
    } else {
      for (let i = this.in2.length; i >= numberOfItems; i--) {
        this.in2.removeAt(i);
      }
    }
  }

  // convenience getters for easy access to form fields
  get f2() {
    return this.chargeFormIT.controls;
  }
  get in2() {
    return this.f2.chargeIT as FormArray;
  }

  // convenience getters for easy access to form fields
  get f() {
    return this.interestForm.controls;
  }
  get in() {
    return this.f.interest as FormArray;
  }

  get iloan() {
    return this.floan.loan as FormArray;
  }

  get floan() {
    return this.loanForm.controls;
  }

  check(accountType) {
    if (accountType) {
      console.log(this.snack.open);
      this.snack.open("chargeInfo detail is empty", "Error", {
        duration: 4000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    }
  }

  checkbox(event) {
    console.log(event.checked);
  }

  assignAccountType(accountType) {
    this.accountType = accountType;
  }

  Done() {
    this.router.navigate([`/cardaccount/underwritingStage/list`]);
  }
  Back() {
    this.router.navigate([`/cardaccount/applicationEnrichment/list`]);
  }
}
