import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { RouterModule, Routes } from '@angular/router';
import { ApplicationEnrichmentComponent } from './application-enrichment/application-enrichment.component';
import { ChargeDetailsComponent } from './application-enrichment/charge-details/charge-details.component';
import { EnrichmentSummaryComponent } from './application-enrichment/enrichment-summary/enrichment-summary.component';
import { IntrestDetailsComponent } from './application-enrichment/intrest-details/intrest-details.component';
import { ApplicationEntryStageComponent } from './application-entry-stage/application-entry-stage.component';
import { CardAccountServicesComponent } from './application-entry-stage/card-account-services/card-account-services.component';
import { CardCustomerInformationComponent } from './application-entry-stage/card-customer-information/card-customer-information.component';
import { CardPrefrencesComponent } from './application-entry-stage/card-prefrences/card-prefrences.component';
import { CardInitiationComponent } from './application-entry-stage/card-initiation/card-initiation.component';
import { FinancialDetailsComponent } from './application-entry-stage/financial-details/financial-details.component';
import { AssessmentStageComponent } from './assessment-stage/assessment-stage.component';
import { CardAccountComponent } from './card-account.component';
import { DigitalSignNowOrLaterComponent } from './digital-signing/digital-sign-now-or-later/digital-sign-now-or-later.component';
import { DigitalSigningComponent } from './digital-signing/digital-signing.component';
import { UploadSignatureOrEsignComponent } from './digital-signing/upload-signature-or-esign/upload-signature-or-esign.component';
import { CardaccountApplicationAssessmentComponent } from './cardaccount-application-assessment/cardaccount-application-assessment.component';
import { DocumentDetailsComponent } from './document-upload/document-details/document-details.component';
import { DocumentUploadComponent } from './document-upload/document-upload.component';
import { ApprovalDetailsComponent } from './supervisor-approval-stage/approval-details/approval-details.component';
import { SupervisorApprovalStageComponent } from './supervisor-approval-stage/supervisor-approval-stage.component';
import { CardaccountQualitativeScoreboardComponent } from './underwriting-stage/cardaccount-qualitative-scoreboard/cardaccount-qualitative-scoreboard.component';
import { UnderwritingCreditRatingComponent } from './underwriting-stage/underwriting-credit-rating/underwriting-credit-rating.component';
import { UnderwritingStageComponent } from './underwriting-stage/underwriting-stage.component';
// import { UploadLinkToEmailComponent } from './document-upload/upload-link-to-email/upload-link-to-email.component';
import { UnderwritingStageSummaryComponent } from './underwriting-stage/underwriting-stage-summary/underwriting-stage-summary.component';


export const CardAccountRoutes: Routes = [
  {
    path: '',
    component: CardAccountComponent,
    children: [
      {
        path: '',
        redirectTo: 'applicationEntryStage/list',
        pathMatch: 'full'
      },
      {
        path: 'applicationEntryStage/list',
        component: ApplicationEntryStageComponent,
      },
      {
        path: 'documentupload/list/document-details',
        component: DocumentDetailsComponent,
      },
      {
        path: 'applicationEntryStage/list/financialDetails',
        component: FinancialDetailsComponent,
      },
      {
        path: 'applicationEntryStage/list/customerInformation',
        component: CardCustomerInformationComponent,
      },
      {
        path: 'applicationEntryStage/list/accountServices',
        component: CardAccountServicesComponent,
      },
      {
        path: 'applicationEntryStage/list/cardPrefrence',
        component: CardPrefrencesComponent,
      },
      {
        path: 'applicationEntryStage/list/cardInitiation',
        component: CardInitiationComponent,
      },
      {
        path: 'documentupload/list',
        component: DocumentUploadComponent,
      },
      // {
      //   path: 'documentupload/link',
      //   component: UploadLinkToEmailComponent,
      // },
      {
        path: 'applicationEnrichment/list',
        component: ApplicationEnrichmentComponent,
      },
      {
        path: 'applicationEnrichment/intrest-details',
        component: IntrestDetailsComponent,
      },
      {
        path: 'applicationEnrichment/charge-details',
        component: ChargeDetailsComponent,
      },
      {
        path: 'applicationEnrichment/Summary',
        component: EnrichmentSummaryComponent,
      },
      {
        path: 'underwritingStage/list',
        component: UnderwritingStageComponent,
      },
      {
        path: 'underwritingStage/summary',
        component: UnderwritingStageSummaryComponent,
      },
      {
        path: 'underwritingStage/credit-rating',
        component:UnderwritingCreditRatingComponent,
      },
      {
        path:'underwritingStage/qualitative',
        component:CardaccountQualitativeScoreboardComponent,
      },
      {
        path: 'assessmentStage/list',
        component: AssessmentStageComponent,
      },
      {
        path:'assessmentStage/application-assessment/list',
        component:CardaccountApplicationAssessmentComponent,
      },
      {
        path: 'SupervisorApprovalStage/list',
        component: SupervisorApprovalStageComponent,
      },
      {
        path: 'SupervisorApprovalStage/list/approvalDetails',
        component: ApprovalDetailsComponent,
      },
      {
        path: 'DigitalSigning/list',
        component: DigitalSigningComponent,
      },
      {
        path: 'digitalsigning/options',
        component: DigitalSignNowOrLaterComponent,
      },
      {
        path: 'digitalsigning/upload',
        component: UploadSignatureOrEsignComponent,
      }
  
  
    ]
  }
];