import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { CurrencyPipe, Location } from '@angular/common';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AccountService } from 'app/views/card-account/account.service';
import { Console } from 'console';

@Component({
  selector: 'app-cardaccount-application-assessment',
  templateUrl: './cardaccount-application-assessment.component.html',
  styleUrls: ['./cardaccount-application-assessment.component.scss'],
})
export class CardaccountApplicationAssessmentComponent implements OnInit {
  currency: string;
  public assessmentForm: FormGroup;
  isAssessmentReport: boolean = false;
  arrayUserRecommendation = [
    'Recommended For Approval',
    'Pending For Documents',
    'Recommended for Review',
  ];
  arraySystemRecommendation = [
    'Recomended For Approval',
    'Recommended For Reject',
  ];
  cardId: any;
  curr: string = '';
  isCardAssessmentStageDone: boolean = false;
  requestedCardLimit: any;
  recommendedCardLimit: any;
  approvedCardLimit: string;
  currencySymboll: string;
  currencySymbol: boolean = true;
  //    currency_symbols = {
  //     'USD': '$', // US Dollar
  //     'EUR': '', // Euro
  //     'GBP': '', // British Pound Sterling
  //     'ILS': '', // Israeli New Sheqel
  //     'INR': '', // Indian Rupee
  //     'JPY': '', // Japanese Yen
  //     'KRW': '', // South Korean Won
  //     'NGN': '', // Nigerian Naira
  //     'PHP': '', // Philippine Peso
  //     'PLN': 'z', // Polish Zloty
  //     'PYG': '', // Paraguayan Guarani
  //     'THB': '', // Thai Baht
  //     'UAH': '', // Ukrainian Hryvnia
  //     'VND': '', // Vietnamese Dong
  //     'CNY': '', // Chinese Yuan Currency
  //     'ZAR': 'R', // South Africa Rand
  // };
  //localCurrencyArr=['CNY','GBP','INR','USD','ZAR','GBP'];
  currencySymbols=[
    {currency:'USD', symbol:'$'},{currency:'EUR', symbol:''},
    {currency:'JPY', symbol:''},
    {currency:'GBP', symbol:''},
    {currency:'ILS', symbol:''},
    {currency:'KRW', symbol:''},
    {currency:'NGN', symbol:''},
    {currency:'PHP', symbol:''},
    {currency:'PLN', symbol:'z'},
    {currency:'THB', symbol:''},
    {currency:'UAH', symbol:''},
    {currency:'VND', symbol:''},
    {currency:'CNY', symbol:''},
    { currency: 'ZAR', symbol: 'R'},
    {currency:'INR', symbol:''},
  ]
  currencyArr: { currency: string; symbol: string; }[];
  currencytype: string;

  constructor(
    private router: Router,
    private snack: MatSnackBar,
    private cdr: ChangeDetectorRef,
    private apiService: ApiService,
    private _location: Location,
    private ls: LocalStoreService,
    private fb: FormBuilder,
    private accountService: AccountService,
    private currencyPipe: CurrencyPipe
  ) {}

  ngOnInit(): void {
    this.curr = localStorage.getItem('Currency Type');
    this.cardId = this.ls.getItem('cardId');
    this.isAssessmentReport = this.ls.getItem('CARD_ASSESSMENT_DONE');
    this.isCardAssessmentStageDone = this.ls.getItem(
      'CARD_ASSESSMENT_STAGE_DONE'
    );
    this.buildAssessmentForm({});
    if (this.isAssessmentReport == false) {
      this.buildAssessmentForm();
    } else {
      this.getCardAssessmentDetails();
    }
  }

  buildAssessmentForm(item?, isResponse?: boolean) {
    this.assessmentForm = this.fb.group({
      // cardId:[data?data.cardId:'952'],
      // agencyId:[data?data.agencyId : 0],
      // cardId:[item?item.cardId:'1'],
      // assessmentId:[item?item.assessmentId:'1'],

      cardId: [this.cardId],
      assessmentId: [(item.assessmentId = '1')],
      requestedCardLimit: [(item.requestedCardLimit = '15000')],
      systemRecommendation: [
        // (item.systemRecommendation = 'Recommended for Approval'),[Validators.required]
        item ? item.systemRecommendation : '',
        [Validators.required],
      ],
      approvedCardLimit: [(item.approvedCardLimit = '10000')],
      // requestedCardLimit: [item ? item.requestedCardLimit: '  15000'],
      // systemRecommendation:[item ? item.systemRecommendation:'Recommended for Approval'],
      // approvedCardLimit:[item ? item.approvedCardLimit: '10000'],
      userRecommendation: [
        item ? item.userRecommendation : '',[Validators.required]
      ],
      recommendedCardLimit: [
        item ? item.recommendedCardLimit : '',Validators.compose(
          [Validators.required,Validators.max(10000)])
       
      ],
      remark: [item ? item.remark : ''],
    });
    console.log('curr', this.curr);
    if (item) {
      // if (this.curr === 'INR') {
      //   //this.recommendedCardLimit = "" + ' ' + this.assessmentForm.value.recommendedCardLimit;
      //   this.approvedCardLimit =
      //     '' + ' ' + this.assessmentForm.value.approvedCardLimit;
      //   this.requestedCardLimit =
      //     '' + ' ' + this.assessmentForm.value.requestedCardLimit;
      //   this.currencySymboll = '' + ' ';
      //   console.log('reccommendedCardLimit:', this.recommendedCardLimit);
      // } else {
      //   // this.recommendedCardLimit = "$" + ' ' + this.assessmentForm.value.recommendedCardLimit;
      //   this.approvedCardLimit =
      //     '$' + ' ' + this.assessmentForm.value.approvedCardLimit;
      //   this.requestedCardLimit =
      //     '$' + ' ' + this.assessmentForm.value.requestedCardLimit;
      //   this.currencySymboll = '$' + ' ';
      // }
      for(let i=0;i<this.currencySymbols.length;i++){
        this.currencyArr= this.currencySymbols.filter((fltered)=>{
         return this.curr===fltered.currency;
        })
        console.log("this.currencyArr",this.currencyArr);
      }
      this.currencySymboll = this.currencyArr[0].symbol;  
      this.approvedCardLimit = this.currencySymboll + ' ' + this.assessmentForm.value.approvedCardLimit;
      this.requestedCardLimit = this.currencySymboll + ' ' + this.assessmentForm.value.requestedCardLimit;
      this.recommendedCardLimit = this.currencySymboll + ' '+ this.assessmentForm.value.reccommendedCardLimit;


      //  this.assessmentForm.controls.recommendedCardLimit.setValue(this.curr);
      this.assessmentForm.controls.approvedCardLimit.setValue(
        this.approvedCardLimit
      );
      this.assessmentForm.controls.requestedCardLimit.setValue(
        this.requestedCardLimit
      );
      // this.assessmentForm.controls.recommendedCardLimit.setValue(this.recommendedCardLimit);//editable field
    }
  }

  transformAmount(element) {
    if (this.curr === 'INR') {
      console.log(this.currencySymbol);
      this.currencySymboll = this.currencyPipe.transform(
        this.currencySymboll,
        ''
      );
      console.log(this.currencySymbol);
      element.target.value = this.currencySymboll;
    } else {
      this.currencySymboll = this.currencyPipe.transform(
        this.currencySymboll,
        '$'
      );
      element.target.value = this.currencySymboll;
    }
    console.log(element.target.value);
  }

  keyUpEvent(event: any){
    console.log('event', event.target.value);
    // var final=event.target.value+ this.currencySymboll;
    let finalNum = event.target.value.toString().replace(/,/g, '').split('.');
    // finalNum[0] = finalNum[0].replace(
    //   /\B(?=(\d{1})+(?!\d))/g,
    //   this.currencySymboll
    // );
    let final = finalNum.join('.');
    // let final = finalNum.slice(0, 5);
    console.log('final', final);
    console.log('finalLength', final.length);
    //this.assessmentForm.controls.recommendedCardLimit.setValue(this.currencySymboll);
    this.assessmentForm.controls.recommendedCardLimit.setValue(final);
  }

  _keyPress1(event: any) {
    const pattern = /^[0-9]*\.?[0-9]*$/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
    const input = event.target.value;
    if (input.length === 0 && event.which === 48) {
      event.preventDefault();
    }
  }

  getCardAssessmentDetails() {
    this.apiService
      .fetchcardAssessmentDetailsById(this.cardId)
      //this.apiService.fetchcardAssessmentDetails()
      .subscribe((resp: any) => {
        console.log(resp);
        this.buildAssessmentForm(resp, true);
      });
  }

  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }

  goNext(val) {
    console.log('val', val.approvedCardLimit);
    console.log('val.approvedCardLimit', val.approvedCardLimit.length);
    console.log('val', val.recommendedCardLimit);
    console.log('val.recommendedCardLimit',val.recommendedCardLimit.length);
    val.approvedCardLimit = val.approvedCardLimit.slice(
      2,
      val.approvedCardLimit.length - 1
    );
    val.requestedCardLimit = val.requestedCardLimit.slice(
      2,
      val.requestedCardLimit.length - 1
    );
    //let num = val.recommendedCardLimit.length;
    //var final = val.recommendedCardLimit.slice(1, num - 1);
   // console.log('final', final);
   // final = this.removeComma(final);
   // console.log('final', final);
   // val.recommendedCardLimit = final;
    this.apiService.cardAssessmentDetails(val).subscribe((data) => {
      console.log('data', data);
      if (data != null) {
        this.snack.open('Assessment details saved successfully!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right',
        });
        this.cdr.markForCheck();
      }
    });
    this.next();
  }
  removeComma(num: any) {
    var numb = num.replace(/,/g, '');
    console.log('numb', numb);
    return numb;
  }

  next() {
    this.ls.setItem('CARD_ASSESSMENT_DONE', true);
    this.ls.setItem('CARD_ASSESSMENT_STAGE_DONE', true);
    this.accountService.refresh.next();
    this.router.navigate([`/cardaccount/SupervisorApprovalStage/list`]);
  }
  goBack() {
    this.router.navigate([`/cardaccount/assessmentStage/list`]);
  }
}
