import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { ApiService } from 'app/shared/services/api.service';
import { QualitativeScoreboardSummary } from 'app/shared/models/qualitativeScoreboardSummary';
import { AssessmentReportSummary } from 'app/shared/models/AssessmentReportSummary';
import { UrlService } from 'app/views/home/url.service';


@Component({
  selector: 'app-underwriting-stage-summary',
  templateUrl: './underwriting-stage-summary.component.html',
  styleUrls: ['./underwriting-stage-summary.component.scss']
})
export class UnderwritingStageSummaryComponent implements OnInit {

  previousUrl: string = '';
  accountId: any;
  qualitativeScoreboardSummary: QualitativeScoreboardSummary;
  assessmentReportSummary: AssessmentReportSummary;
  imagePath = "assets/images/assesment.png";
  cardId: any;
  customerName: any;
  creditRatingValues: any;
  constructor(
    private router: Router,
    private ls: LocalStoreService,
    private apiService: ApiService,
    private urlService: UrlService
  ) { }

  ngOnInit(): void {
    this.cardId = this.ls.getItem('cardId');
    
    this.getAllData(this.cardId);

    this.urlService.previousUrl$
      .subscribe((previousUrl: string) => {
        this.previousUrl = previousUrl
      });


  }

  getAllData(id) {
    this.apiService.getCardQualitativeScorecardDetails(this.cardId).subscribe((response) => {
      this.qualitativeScoreboardSummary = response;
      console.log(this.qualitativeScoreboardSummary);
       console.log(this.qualitativeScoreboardSummary.scoreCardMapping);
    });
   
    this.apiService.getCardCreditRatingDetails(id).subscribe((creditDetails: any) => {
      console.log(creditDetails.customerName);
      this.customerName=creditDetails.customerName;
      this.creditRatingValues=creditDetails;
      });
    }
  
    goBack() {
      this.router.navigate([`cardaccount/underwritingStage/list`]);
    }
  
    done() {
      // this.router.navigate([`/cardaccount/assessmentStage/list/${this.cardId}`]);
      this.router.navigate(["/cardaccount/assessmentStage/list"]);
    }
  

}
