import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatSnackBar } from "@angular/material/snack-bar";
import { Router } from "@angular/router";
import { ApiService } from "app/shared/services/api.service";
import { AppLoaderService } from "app/shared/services/app-loader/app-loader.service";
import { LocalStoreService } from "app/shared/services/local-store.service";
// import { AccountService } from 'app/views/loan-account/account.service';
import { AccountService } from 'app/views/card-account/account.service';



@Component({
  selector: 'app-intrest-details',
  templateUrl: './intrest-details.component.html',
  styleUrls: ['./intrest-details.component.scss']
})
export class IntrestDetailsComponent implements OnInit {

  interestForm: FormGroup;
  accountId: any;
  intrestFlag: Boolean;
  interestResp: any;
  showEditOption: boolean = false;
  resp: {
    intrestType: string;
    intrestRateApplicable: string;
    marginIn: number;
    effectiveRate: number;
  }[];
  accountType: String = "Card Origin";
  cardId: number;
  
  constructor(
    private accountService: AccountService,
    private snack: MatSnackBar,
    private ls: LocalStoreService,
    private api: ApiService,
    private router: Router,
    private formBuilder: FormBuilder,
    private loader: AppLoaderService,
  ) { }

  ngOnInit(): void {
    this.cardId=this.ls.getItem('cardId');
    let isCardInterestDetails = this.ls.getItem('CARD_INTEREST_DETAILS_DONE');
    this.getInterestDetails();
    if (this.cardId != null && isCardInterestDetails) {
      this.showEditOption = true;
      
      this.getInterestDetails();
    } 
    else {
      console.log("else");
      this.showEditOption = false;
      this.api
      .getFlexcubeDetails("intrestDetails", this.accountType)
      .subscribe((response) => {
        if(response) {
          this.onChangeInterest(response.length);
          this.in.patchValue(response);
        }
      });
    }

    this.interestForm = this.formBuilder.group({
      interestInfo: new FormArray([]),
    });
  
  }

  getInterestDetails(){
    // this.loader.open();
    // this.cardId=this.ls.getItem('cardId');
    // console.log(this.cardId)
    this.cardId = this.cardId;
    this.api.cardInterestFetchById(this.cardId).subscribe((resp) => {
      this.loader.close();
      console.log("resp from backend ", resp);
      if(resp) {
        this.resp = resp;
        this.onChangeInterest(this.resp.length);
        this.in.patchValue(this.resp);
      }
    }, (err: any) => {
      // this.loader.close();
    }); 
  }

  onChangeInterest(e) {
    this.cardId=this.ls.getItem('cardId');
    const numberOfItems = e || 0;
    if (this.in.length < numberOfItems) {
      for (let i = this.in.length; i < numberOfItems; i++) {
        this.in.push(
          this.formBuilder.group({
            intrestType: ["Fixed Rate", Validators.required],
            intrestRateApplicable: [5.0, Validators.required],
            marginIn :[null,[Validators.required, Validators.max(100), Validators.min(0)],],
            effectiveRate: [10.0,[Validators.required, Validators.max(100), Validators.min(0)],],
            cardInterestId: [""],
            cardId: [this.cardId],
          })
        );
      }
    } else {
      for (let i = this.in.length; i >= numberOfItems; i--) {
        this.in.removeAt(i);
      }
    }
  }

  get f() {
    return this.interestForm.controls;
  }
  get in() {
    return this.f.interestInfo as FormArray;
  }

  appendPercentage(event) {
    // console.log(event.key)
    console.log(event.target.value);

    if (!event.target.value.length) return;

    if (event.target.value.length === 1) {
      event.target.value = `${event.target.value}%`;
    }
    if (event.target.value.length > 1) {
      event.target.value = `${event.target.value.slice(0, -1)}%`;
    }
  }

  onSubmit() {
    console.log(this.interestForm.value)
    this.api.cardintrestDetailsSave(this.interestForm.value).subscribe(
      (resp) => {
        this.snack.open("Interest Details Saved!", "OK", {
          duration: 4000,
          verticalPosition: "top",
          horizontalPosition: "right",
        });
        this.next();
        console.log("backend response from intrest saving", resp);
      },
      (err) => {
        this.snack.open(
          "Not able to connect to server!",
          "INTERNAL SERVER ERROR",
          {
            duration: 4000,
            verticalPosition: "top",
            horizontalPosition: "right",
          }
        );
      }
    );
    
  }

  next() {
    this.ls.setItem('CARD_INTEREST_DETAILS_DONE', true);
    this.accountService.refresh.next();
    this.router.navigate([`/cardaccount/applicationEnrichment/charge-details`]);
  }

  goBack() {
    this.router.navigate([
      `/cardaccount/applicationEnrichment/list`,
    ]);
  }

  check(accountType) {
    if (accountType) {
      console.log(this.snack.open);
      this.snack.open("Interest detail is empty", "Error", {
        duration: 4000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    }
  }

  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
}
