import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { LocalStoreService } from 'app/shared/services/local-store.service';

import { DigitalViewSummaryComponent } from './digital-view-summary/digital-view-summary.component';

import { AccountService } from '../account.service';

import { DigitalsignSummaryComponent } from './digitalsign-summary/digitalsign-summary.component';

@Component({
  selector: 'app-digital-signing',
  templateUrl: './digital-signing.component.html',
  styleUrls: ['./digital-signing.component.scss']
})
export class DigitalSigningComponent implements OnInit {

  informations = [
    {
      'id':1,
      'title': '1. Digital Signing',
      'content':"Customer can either sign now or sign later",
      'percentage':70,
      'status':'Pending',
      'state': 'application/digital-sign/options',
      'avatar':"assets/images/esign.jpg"

    }
  ];

  accountId:any;
  isEsignScreen:boolean = false;
  constructor(private router:Router,private ls: LocalStoreService,public dialog: MatDialog,private cdr:ChangeDetectorRef, private accountService: AccountService) { }

  ngOnInit(){
    this.accountId = this.ls.getItem('accountId');
    this.isEsignScreen = this.ls.getItem('eSignStage');

    this.accountService.refresh.subscribe((res: any) => {
      this.accountId = this.ls.getItem('accountId');
      this.isEsignScreen = this.ls.getItem('eSignStage');
    
      this.cdr.markForCheck();
    });
  }

  goToDetail(item){
    console.log(item);
    this.router.navigate([`/home/${item.state}`]);
  }
  goToDigitalSign() {
    const dialogRef = this.dialog.open(DigitalsignSummaryComponent, { 
      height: '55%',
      width: '40%',
      disableClose: true });
    dialogRef.afterClosed().subscribe((submit) => {
    })

  }
  goToDigitalSignViewSummary() {
    const dialogRef = this.dialog.open(DigitalViewSummaryComponent, { 
      height: '55%',
      width: '40%',
      disableClose: true });
    dialogRef.afterClosed().subscribe((submit) => {
    })

  }

  goToDigitalSigningOptions() {
    this.ls.setItem('CARD_DIGITAL_STAGE_DONE', true);
    this.accountService.refresh.next();
    this.router.navigateByUrl('/cardaccount/digitalsigning/options');
  }

}