import { Component, OnInit } from '@angular/core';
import { FormGroup, FormArray, FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApplicationEntryStageService } from 'app/shared/services/application-entry-stage.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AccountService } from 'app/views/card-account/account.service';
import { number } from 'ngx-custom-validators/src/app/number/validator';

@Component({
  selector: 'app-underwriting-credit-rating',
  templateUrl: './underwriting-credit-rating.component.html',
  styleUrls: ['./underwriting-credit-rating.component.scss'],
})
export class UnderwritingCreditRatingComponent implements OnInit {
  creditDetailsForm: FormGroup;
  customerNames: any[] = [];
  cardId: any;
  isCardCreditRatingDetails: any;
  customerName: any;
  disableTest: boolean;
  disableUjjivan: boolean;
  disableFastTrack: boolean;
  disableAddAgency: boolean;
  deletecount:number;
  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private ls: LocalStoreService,
    private appApiService: ApplicationEntryStageService,
    private snack: MatSnackBar,
    private api: ApiService,
    private accountService: AccountService
  ) {}

  ngOnInit(): void {
    this.cardId = this.ls.getItem('cardId');

    this.customerName = this.ls.getItem('primaryCustomerName');
    this.isCardCreditRatingDetails = this.ls.getItem('CARD_CREDIT_RATING_DONE');
    this.getCustomerInformationDetails();
    this.buildCreditForm();

    if (this.isCardCreditRatingDetails == false) {
      this.buildCreditForm();
    } else {
      this.gettingCreditRatingDetail();
    }
  }

  get f() {
    return this.creditDetailsForm.controls;
  }
  get c() {
    return this.f.agencyRating as FormArray;
  }

  buildCreditForm(data?, isResponse?: boolean) {
    console.log(data);

    this.creditDetailsForm = this.formBuilder.group({
      //   customerName:[data && data.customerName? data.customerName:'',Validators.required],
      // loanAccountId:this.loanaccountID,
      cardId: this.cardId,
      //cardId:data?data.cardId:'952',
      // customerName:data? data.customerName:'',
      customerName: data ? data.customerName : this.customerNames[0],
      // customerName:data?data.customerName:this.customerName,
      agencyRating: this.formBuilder.array([this.addModFormGroup(data)]),
    });

    console.log(this.creditDetailsForm);
  }
  addFormGroup() {
    this.c.push(
      this.formBuilder.group({
        agencyId: [0],
        agencyName: ['', Validators.required],
        ratings: ['', Validators.required],
        // remarks: ['',Validators.required],
        // agencyName: [''],
        // ratings: [''],
        // remarks: [''],
      })
    );
    for (let i=0;i<this.c.length;i++){
      this.deletecount=i
    }
  }

  goBack() {
    this.router.navigate(['/cardaccount/applicationEnrichment/list']);
  }

  addModFormGroup(data): FormGroup {
    console.log(data, 'inside addModeFormGrp');
    return this.formBuilder.group({
      // cardId:[data?data.cardId:'952'],
      cardId: [this.cardId],
      agencyId: [data ? data.agencyId : 0],
      agencyName: [
        data ? data.agencyRating[0].agencyName : '',
        Validators.required,
      ],
      ratings: [data ? data.agencyRating[0].ratings : '',Validators.compose(
        [Validators.required,Validators.max(5)])],
      remarks: [data ? data.agencyRating[0].remarks : ''],
      // agencyName: [data?data.agencyRating[0].agencyName : ''],
      // ratings: [data?data.agencyRating[0].ratings : ''],
      // remarks: [data?data.agencyRating[0].remarks : ''],
    });
  }

  getCustomerInformationDetails() {
    this.appApiService
      .fetchCustomersByCardAccId(this.cardId, 'cardOrigin')
      .subscribe(
        (resp: any) => {
          console.log('resp for customer information names ', resp.data);
          for (let index = 0; index < resp.data.length; index++) {
            if (resp.data[index].middleName == null) {
              this.customerNames[index] =
                resp.data[index].firstName + ' ' + resp.data[index].lastName;
            } else {
              this.customerNames[index] =
                resp.data[index].firstName +
                ' ' +
                resp.data[index].middleName +
                ' ' +
                resp.data[index].lastName;
            }
          }
          console.log('customer name ', this.customerNames);
          if (this.customerNames) {
            console.log('If condtion');
            this.creditDetailsForm
              .get('customerName')
              .setValue(this.customerNames[0]);
          }
        },
        (err: any) => {}
      );
  }
  gettingCreditRatingDetail() {
    this.api.getCardCreditRatingDetails(this.cardId).subscribe((resp: any) => {
      console.log(resp);
      this.buildCreditForm(resp, true);
    });
  }
  removeAgency(i:number) {
    
    this.c.removeAt(this.deletecount);
    this.deletecount=this.c.length-1
  }

  OnSubmit(val) {
    console.log('val', val);
    val.accountType = 'cardOrigin';
    this.api.saveCreditDetails(val).subscribe(
      (data: any) => {
        console.log(data);
        if (data === 200 || data !== null) {
          this.snack.open(' Credit Details Saved!', 'OK', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right',
          });
          this.ls.setItem('CARD_CREDIT_RATING_DONE', true);
          this.accountService.refresh.next();
          this.router.navigate([`/cardaccount/underwritingStage/qualitative`]);
        } else if (data === 500) {
          this.snack.open(
            'Not able to connect to server!',
            'INTERNAL SERVER ERROR',
            {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right',
            }
          );
        }
      },
      (err) => {
        console.log(err);
      }
    );
    this.router.navigate([`/cardaccount/underwritingStage/qualitative/`]);
  }
  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }

  onClick() {
   for(let i=0; i<3; i++) {     
     if(this.creditDetailsForm.value.agencyRating[i].agencyName=="Test Agency") {
       this.disableTest=true;
       this.disableUjjivan=false;
       this.disableFastTrack=false;
      }    
     if(this.creditDetailsForm.value.agencyRating[i].agencyName=="Ujjivan Agency") {
       this.disableUjjivan=true;
       this.disableFastTrack=false;
       this.disableTest=false;
      }    
     if(this.creditDetailsForm.value.agencyRating[i].agencyName=="Fast Track Agency") {
       this.disableFastTrack=true;
       this.disableUjjivan=false;
       this.disableTest=false;


      }
    }
    if(this.creditDetailsForm.value.agencyRating.length==3) {
      this.disableAddAgency=true;
    }
  }

}
