import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { ApiService } from 'app/shared/services/api.service';
import { DialogService } from 'app/shared/services/dialogs/dialog.service';
import { Router } from '@angular/router';
import { AccountService } from 'app/views/card-account/account.service';

@Component({
  selector: 'app-assessment-stage',
  templateUrl: './assessment-stage.component.html',
  styleUrls: ['./assessment-stage.component.scss']
})
export class AssessmentStageComponent implements OnInit {
  isCardAssessmentStageDone:boolean=false;
  isAssessmentReport:boolean=false;
  cardId:any;

  constructor(
    private ls: LocalStoreService,private router: Router, private dialogService: DialogService, 
    private cdr: ChangeDetectorRef,
     private accountService: AccountService, private apiService: ApiService
  ) { }
  

  ngOnInit(): void {
    this.cardId=this.ls.getItem('cardId');
    this.isAssessmentReport=this.ls.getItem('CARD_ASSESSMENT_DONE');
    this.isCardAssessmentStageDone=this.ls.getItem('CARD_ASSESSMENT_STAGE_DONE');
  }

  goToCardAssessmentSummary(){
    this.dialogService.cardAccountAssessmentSummaryDialog();
  }
  goToCardAppAssessmentSummary(){
    this.dialogService.cardAccAssessmentSummaryDialog();
  }

  openLoanQualitativeDialog(){
    this.dialogService.loanQualitativeSummaryDialog();
  }

}
