import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import * as moment from 'moment';
import Swal from 'sweetalert2';
import { AccountService } from 'app/views/card-account/account.service';

@Component({
  selector: 'app-financial-details',
  templateUrl: './financial-details.component.html',
  styleUrls: ['./financial-details.component.scss']
})
export class FinancialDetailsComponent implements OnInit {
  cardFinancialDetailsForm:FormGroup;
  arrayEmploymentType: string[];
  arrayEmploymentCategory: string[];
  //formBuilder: any;
  incomeErrMsg: string;
  disableNext: boolean = false;
  ExpensesErrMsg: any;
  LiabilitiesErrMsg: any;
  assetErrMsg: string;
  liabilitiesErrMsg: string;
  expensesErrMsg: string;
  cardId: any;
  showEditOption:boolean=false;
  minDate = new Date();
   maxDate = new Date();
  applicant: any;
  applicantName: any;
  lastData:any;
  currencySymbol: string;
  currencytype: string;


  constructor(   private formBuilder: FormBuilder, private api: ApiService,private snack: MatSnackBar, private accountService: AccountService,
    private router: Router,private ls: LocalStoreService, private cdr:ChangeDetectorRef,
   ) { 
    this.lastData = new Date().toISOString().slice(0,10);
   }

  ngOnInit(): void {
    console.log(history.state.amount);
    
    this.cardId = this.ls.getItem('cardId');
    this.currencytype=localStorage.getItem('Currency Type');
    if(this.currencytype==="INR"){
      this.currencySymbol=""
    }else if(this.currencytype==="USD"){
      this.currencySymbol="$"
    }else if(this.currencytype==="CNY"){
      this.currencySymbol=""
    }else if(this.currencytype==="GBP"){
      this.currencySymbol=""
    }else if(this.currencytype==="ZAR"){
      this.currencySymbol="R"
    }
    // this.cardId=this.ls.getItem("cardId");
   // this.applicant=this.ls.getItem('Card_Applicant_name');
    this.applicantName=this.ls.getItem('primaryCustomerName');
    let isFinancialScreen: boolean = JSON.parse(this.ls.getItem('CARD_FINANCIAL_DONE'));
    this.getEmploymentType("fin_employment_type");
    this.getEmploymentCategory("employment_category");
    // setTimeout(() => {
    //   this.buildFinacialFormGroup();
    // },200);
      
  
    if (this.cardId != null && isFinancialScreen) {
      console.log("fggd");
      
      this.showEditOption = true;
      this.getCardFinancialDetailsSummary();
    } else {
      this.showEditOption = false;
      setTimeout(() =>{
        this.buildFinacialFormGroup();
      },200)
      
    }


    // this.createFinancialDetail();
  }
  getCardFinancialDetailsSummary(){
    
    this.api.getCardFinancialDetails(this.cardId)
    .subscribe((result: any) => {

      console.log(result);

      let index = result.length-1;
      console.log(result[index]);
      
      this.buildFinacialFormGroup(result[index]);

    })
  }
  getEmploymentType(key: string) {
    this.api.getDropDownValues(key).subscribe((data) => {
      this.arrayEmploymentType = data;
    });
  }
  getEmploymentCategory(key: string) {
    this.api.getDropDownValues(key).subscribe((data) => {
      this.arrayEmploymentCategory = data;
    });
  }

  _keyPress(event: any) {
    // console.log("key",event);
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
        event.preventDefault();
    }
   }
   buildFinacialFormGroup(data?) {
     console.log(data);
    this.cardFinancialDetailsForm = this.formBuilder.group({
      // Financial Details
     // applicantName: [this.applicantName, Validators.required],
      applicantName: [data ? data.applicantName : this.ls.getItem('Card_Applicant_name'), Validators.required],
      currentlyWorking: [data ? data.currentlyWorking : ''],
      applicantTotalIncome: [data ? data.applicantTotalIncome : 0, Validators.required],
      applicantTotalExpense: [data ? data.applicantTotalExpense : 0, Validators.required],
      lastUpdatedon: [data && data?.lastUpdatedon ? new Date(data.lastUpdatedon) : ''],
      //limit_end_date: [data ? new Date(data.limit_end_date) : ''],
      
      // Employement Details
      employmentType: [data ? data.employmentType : '', Validators.required],
      employmentCategory: [data ? data.employmentCategory : '', Validators.required],
      employeeNumber: [data ? data.employeeNumber : ''],
      officeName: [data ? data.officeName : ''],
      designation: [data ? data.designation : ''],
      employmentStartDate: [data && data?.employmentStartDate ? new Date(data.employmentStartDate): '', Validators.required],
      employmentEndDate: [data && data?.employmentEndDate ? new Date(data.employmentEndDate): '',],
      
      // Monthly Income
      salary: [data ? data.salary : ''],
      interestIncome: [data ? data.interestIncome : ''],
      bonus: [data ? data.bonus : ''],
      cashGifts: [data ? data.cashGifts : ''],
      business: [data ? data.business : ''],
      pension: [data ? data.pension : ''],
      monthlyIncomeRentals: [data ? data.monthlyIncomeRentals : ''],
      monthlyIncomeOthers: [data ? data.monthlyIncomeOthers : ''],
      monthlyIncomeTotal: [data ? data.monthlyIncomeTotal : 0],
            
      // Monthly Expense
      household: [data ? data.household : ''],
      education: [data ? data.education : ''],
      vehicleMaintenance: [data ? data.vehicleMaintenance : ''],
      monthlyExpenseRentals: [data ? data.monthlyExpenseRentals : ''],
      monthlyExpenseOthers: [data ? data.monthlyExpenseOthers : ''],
      medical: [data ? data.medical : ''],
      travel: [data ? data.travel : ''],
      monthlyExpenseTotal: [data ? data.monthlyExpenseTotal : 0],
      
      // Annual Liability
      propertyLoan: [data ? data.propertyLoan : ''],
      vehicleLoan: [data ? data.vehicleLoan : ''],
      personalLoans: [data ? data.personalLoans : ''],
      overdraft: [data ? data.overdraft : ''],
      cardOutstandings: [data ? data.cardOutstandings : ''],
      liabilityOthers: [data ? data.liabilityOthers : ''],
      liabilityTotal: [data ? data.liabilityTotal : 0],
      
      // Assets
      savingDeposits: [data ? data.savingDeposits : ''],
      properties: [data ? data.properties : ''],
      automobiles: [data ? data.automobiles : ''],
      fixedDeposits: [data ? data.fixedDeposits : ''],
      lands: [data ? data.lands : ''],
      assetOthers: [data ? data.assetOthers : ''],
      stocks: [data ? data.stocks : ''],
      assetTotal: [data ? data.assetTotal : 0],
      
      // accountId; -->need to get from other screens
     // accountId: this.accountId != null ? this.accountId : '',
    }, {validator:(formGroup:FormGroup)=>{
      return this.validateInputGroups(formGroup)
    }
  });

  }  
  validateInputGroups(formgroup:FormGroup) {
    if(formgroup.controls["salary"].value || formgroup.controls["interestIncome"].value || 
    formgroup.controls["bonus"].value || formgroup.controls["cashGifts"].value || 
    formgroup.controls["business"].value || formgroup.controls["pension"].value || 
    formgroup.controls["monthlyIncomeRentals"].value || formgroup.controls["monthlyIncomeOthers"].value) {
     
      this.incomeErrMsg = null
        this.disableNext = true;
      
    }
    else {
      this.incomeErrMsg = "Entering any one field is mandatory for Monthly Income Section";
      this.disableNext = false;
    }
    
    if(formgroup.controls["household"].value || formgroup.controls["education"].value ||
    formgroup.controls["vehicleMaintenance"].value || formgroup.controls["monthlyExpenseRentals"].value ||
    formgroup.controls["monthlyExpenseOthers"].value || formgroup.controls["medical"].value || 
    formgroup.controls["travel"].value) {

      this.ExpensesErrMsg = null;
        this.disableNext = true;
      
    }
    else {
      this.ExpensesErrMsg = "Entering any one field is mandatory for Monthly Expenses Section";
      this.disableNext = false;
    }
    
    if(formgroup.controls["propertyLoan"].value || formgroup.controls["vehicleLoan"].value ||
    formgroup.controls["personalLoans"].value || formgroup.controls["overdraft"].value ||
    formgroup.controls["cardOutstandings"].value || formgroup.controls["liabilityOthers"].value) {
      
      this.LiabilitiesErrMsg = null;
        this.disableNext = true;
      
    }
    else {
      this.LiabilitiesErrMsg = "Entering any one field is mandatory for Annual Liabilities Section";
      this.disableNext = false;
    }
    
    if(formgroup.controls["savingDeposits"].value || formgroup.controls["properties"].value ||
    formgroup.controls["automobiles"].value || formgroup.controls["fixedDeposits"].value ||
    formgroup.controls["lands"].value || formgroup.controls["assetOthers"].value || 
    formgroup.controls["stocks"].value) {

      this.assetErrMsg = null;
        this.disableNext = true;
      
    }
    else {
      this.assetErrMsg = "Entering any one field is mandatory for Asset Section";
      this.disableNext = false;
    }
  }
  
  onSubmit() {
    let startDate = this.converDate(this.cardFinancialDetailsForm.controls.employmentStartDate.value);
    if(startDate && startDate!= "Invalid date"){
    console.log("startDate", startDate);
    this.cardFinancialDetailsForm.value['employmentStartDate'] = startDate;
    }else{
      this.cardFinancialDetailsForm.value['employmentStartDate'] = null;
    }
    let endDate = this.converDate(this.cardFinancialDetailsForm.controls.employmentEndDate.value);
    if(endDate && endDate!= "Invalid date"){
      console.log("startDate", endDate);
      this.cardFinancialDetailsForm.value['employmentEndDate'] = endDate;
    }else{
      this.cardFinancialDetailsForm.value['employmentEndDate'] = null;
    }
    
    //let lastUpdatedon = this.converDate(this.cardFinancialDetailsForm.controls.lastUpdatedon.value);
    let lastUpdatedon =moment(this.cardFinancialDetailsForm.controls.lastUpdatedon.value).format('YYYY-MM-DD HH:MM:SS');
    console.log("lastUpdatedon",lastUpdatedon)
    if(lastUpdatedon && lastUpdatedon!= "Invalid date"){
      console.log("startDate", lastUpdatedon);
      this.cardFinancialDetailsForm.value['lastUpdatedon'] = lastUpdatedon;
    }else{
      this.cardFinancialDetailsForm.value['lastUpdatedon'] = null;
    }

    this.cardFinancialDetailsForm.value.accountType="cardOrigin";
    this.cardFinancialDetailsForm.value.cardId=this.cardId; 
    console.log("submit", this.cardFinancialDetailsForm.value);
    console.log(this.cardFinancialDetailsForm.value)
    var payload: any[]=[];
    payload.push(this.cardFinancialDetailsForm.value)
    console.log(payload);
    // if (!this.cardFinancialDetailsForm.value.applicantName || !this.cardFinancialDetailsForm.value.employmentType || !this.cardFinancialDetailsForm.value.employmentCategory) {
    //   console.log("if--->")
    //   Swal.fire('Please Enter The Mandatory Fields !');
    //   return false;
    // }
    this.api.upsertCardFinancialDetails(payload).subscribe(fincialResp => {

      console.log(fincialResp);
      this.snack.open('Financial Details Saved!', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });

      // this.ls.setItem('limitEntryStage', 'true');
      // this.ls.setItem('LIMIT_FIN_DONE', true);
      //  this.accountService.refresh.next();
       this.next();
      // limit-entry-stage/view-summary
      //this.router.navigate([`/home/application/enrichment/list`, this.accountId]);

    }, err => {
      this.snack.open('Error Unable to Save!', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
    })

  }
  goBack(){
    this.router.navigate([`/cardaccount/applicationEntryStage/list/customerInformation`]);
  }
  next(){
    this.ls.setItem('CARD_FINANCIAL_DONE', true)
    this.accountService.refresh.next();
    this.router.navigate([`/cardaccount/applicationEntryStage/list/cardPrefrence`]);
   
  }
  converDate(value) {
    // let date = moment(value).format('YYYY-MM-DDThh:mm:ss');
    let date = moment(value).format('YYYY-MMM-DD');
    console.log("date", date);
    if (date)
      return date;
  }
  letterOnly(event) {
    var charCode = event.keyCode;
            if ((charCode > 64 && charCode < 91) || (charCode > 96 && charCode < 123) || charCode == 8)
                return true;
            else
                return false;
  }
  // Total Monthly Income
  calculateTotalMonthlyIncome(value) {
    let income:number = Number(value.salary) + Number(value.business) + 
    Number(value.interestIncome) + Number(value.pension) + 
    Number(value.bonus) + Number(value.monthlyIncomeOthers) + 
    Number(value.monthlyIncomeRentals) + Number(value.cashGifts);
    
    // this.cardFinancialDetailsForm.controls.monthlyIncome.get('monthlyIncomeTotal').setValue(income);
    this.cardFinancialDetailsForm.controls.monthlyIncomeTotal.setValue(income);
    this.cdr.markForCheck();
    this.cdr.detectChanges();

    this.calculateTotalIncomeAndExpense(this.cardFinancialDetailsForm.value);
  }

  // Total Monthly Expense
  calculateTotalMonthlyExpenses(value) {
    let expense:number = Number(value.household) + Number(value.education) + 
      Number(value.vehicleMaintenance) + Number(value.monthlyExpenseOthers) + 
      Number(value.medical) + Number(value.travel) + 
      Number(value.monthlyExpenseRentals);

    // this.cardFinancialDetailsForm.controls.monthlyExpense.get('monthlyExpenseTotal').setValue(expense);
    this.cardFinancialDetailsForm.controls.monthlyExpenseTotal.setValue(expense);
    this.cdr.markForCheck();
    this.cdr.detectChanges();

    this.calculateTotalIncomeAndExpense(this.cardFinancialDetailsForm.value);
  }

  // Total Liabilities
  calculateTotalLiabilities(value) {
    let liability:number = Number(value.propertyLoan) + Number(value.personalLoans) + 
    Number(value.overdraft) + Number(value.vehicleLoan) + 
    Number(value.cardOutstandings) + Number(value.liabilityOthers);

    // this.cardFinancialDetailsForm.controls.annualLiability.get('liabilityTotal').setValue(liability);
    this.cardFinancialDetailsForm.controls.liabilityTotal.setValue(liability);
    this.cdr.markForCheck();
    this.cdr.detectChanges();
    
    this.calculateTotalIncomeAndExpense(this.cardFinancialDetailsForm.value);
  }

  // Total Assets
  calculateTotalAssets(value) {
    let asset:number = Number(value.savingDeposits) + Number(value.properties) + 
    Number(value.fixedDeposits) + Number(value.stocks) + 
    Number(value.automobiles) + Number(value.lands) + Number(value.assetOthers);

    // this.cardFinancialDetailsForm.controls.assets.get('assetTotal').setValue(asset);
    this.cardFinancialDetailsForm.controls.assetTotal.setValue(asset);
    this.cdr.markForCheck();
    this.cdr.detectChanges();
    
    this.calculateTotalIncomeAndExpense(this.cardFinancialDetailsForm.value);
  }

  // Total Income, Expense and Final Total
  calculateTotalIncomeAndExpense(value) {

    let totalIncome:number = (value.monthlyIncomeTotal*12) + value.assetTotal;
    let totalExpense:number =(value.monthlyExpenseTotal*12) + value.liabilityTotal;

    this.cardFinancialDetailsForm.controls.applicantTotalIncome.setValue(totalIncome);
    this.cardFinancialDetailsForm.controls.applicantTotalExpense.setValue(totalExpense);
    this.cdr.markForCheck();
    this.cdr.detectChanges();
   }

}
