import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { LocalStoreService } from 'app/shared/services/local-store.service';


@Component({
  selector: 'app-document-upload',
  templateUrl: './document-upload.component.html',
  styleUrls: ['./document-upload.component.scss']
})
export class DocumentUploadComponent implements OnInit {

  isLoanApplicationEntryDone :boolean=false;
  isLoanDetailScreen :boolean=false;
 
  isDocumentUploadDone: boolean = false;
  isDocumentUpload: boolean = false;


  constructor(
    private router: Router,
    private ls: LocalStoreService
  ) { }

  ngOnInit(): void {
    this.isDocumentUpload = this.ls.getItem('CARD_DOCUMENT_DONE');
    if (this.isDocumentUpload) {
      this.isDocumentUploadDone = true;
    }
  }


  documentdetails(){
    this.router.navigate([
      `/cardaccount/documentupload/list/document-details`,
    ]);

  }

}
